    paragraph1: 'Ezra and Nehemiah have in the past, especially in ancient Jewish scholarship been described as a unity, rather than two books. Though they are considered as, and should be taken as two separate books they share a strong identity. It is reasonable to identify Ezra, a literate Jewish priest and strong leader, as the primary author of Ezra, especially given that the narrative becomes first person in chapter  7.',
    paragraph1b: 'Ezra narrates the return of Israel, specifically the Judeans, the tribe of Benjamin, and the priests, the Levites, to Jerusalem. J. Brian Tucker (The Moody Bible Commentary, Moody Publishers Chicago, p. 637 ) writes that “the theme of the book of Ezra is the restoration of the covenant identity of Israel through God’s providence.”  It is also one of the most remarkable and specific fulfillments of prophesy, as shared by Isaiah, in the Old Testament. Ezra wrote with a clear vision: ',
