    paragraph2: 'Chapters 1-6 describe the first return of the contingent of 42,360 people charged with the rebuilding project and the challenges they, and succeeding generations, would face.',
    paragraph2b: 'Ezra himself was a priest, descended from Aaron, knowledgeable and skilled in remembering and applying the Law of Moses, and a talented scribe, writing with precision and evocative remembrance. Like Ruth, Esther, Mordecai, and Nehemiah he sought God’s purpose and direction in a bleak landscape where he was a captive and surrounded by opposition. He had won the attention and favor of the king who was Artaxerxes I. Artaxerxes was the son of Xerxes the Great, the king we met in the Book of Esther, the king who took Esther as his queen, and who was assassinated in 465 B.C.',
    paragraph2c: 'But before Artaxerxes the Book of Ezra begins with a panoramic sweep, taking us back to the first year of Cyrus the Great, king of Persia; almost 60 years before the events in Esther began. Cyrus started his rise to power in 559 B.C.,  as an assisting king to his father  when Persia was not a great empire, but a small, lesser client state of the Medes. He conquered the Medes, the area that is now modern Turkey, and then the Babylonians who had first conquered Judah. It was the Babylonians who had destroyed Jerusalem, and then brought the Jews into exile.',
    content: 'Ezra and Nehemiah have in the past, especially in ancient Jewish scholarship been described as a unity, rather than two books. Though they are considered as, and should be taken as two separate books they share a strong identity. It is reasonable to identify Ezra, a literate Jewish priest and strong leader, as the primary author of Ezra, especially given that the narrative becomes first person in chapter 7. Ezra narrates the return of Israel, specifically the Judeans, the tribe of Benjamin, and the priests, the Levites, to Jerusalem. J. Brian Tucker (The Moody Bible Commentary, Moody Publishers Chicago, p. 637 ) writes that “the theme of the book of Ezra is the restoration of the covenant identity of Israel through God’s providence.” It is also one of the most remarkable and specific fulfillments of prophesy, as shared by Isaiah, in the Old Testament. Ezra wrote with a clear vision: To show us that God actively works through history and across time, and has a clear design in mind; even amid chaotic and imperfect circumstances. To show, as does the Book of Esther, that God often uses the ungodly and the worldly, people who do not know him, to accomplish his greater purposes. To show that God kept his promises to restore his people, and today continues to keep his promises to us. To show that God was specifically invested in rebuilding the Temple and Jerusalem itself, and commissioned people to restore it. To show that God had a greater and superseding purpose, greater even than restoring Israel – preparing the stage and time for the coming of Christ. Chapters 1-6 describe the first return of the contingent of 42,360 people charged with the rebuilding project and the challenges they, and succeeding generations, would face. Ezra himself was a priest, descended from Aaron, knowledgeable and skilled in remembering and applying the Law of Moses, and a talented scribe, writing with precision and evocative remembrance. Like Ruth, Esther, Mordecai, and Nehemiah he sought God’s purpose and direction in a bleak landscape where he was a captive and surrounded by opposition. He had won the attention and favor of the king who was Artaxerxes I. Artaxerxes was the son of Xerxes the Great, the king we met in the Book of Esther, the king who took Esther as his queen, and who was assassinated in 465 B.C. But before Artaxerxes the Book of Ezra begins with a panoramic sweep, taking us back to the first year of Cyrus the Great, king of Persia; almost 60 years before the events in Esther began. Cyrus started his rise to power in 559 B.C., as an assisting king to his father when Persia was not a great empire, but a small, lesser client state of the Medes. He conquered the Medes, the area that is now modern Turkey, and then the Babylonians who had first conquered Judah. It was the Babylonians who had destroyed Jerusalem, and then brought the Jews into exile.'
