    paragraph1: 'Cyrus began his rule of the greater Persian Empire in 538 B.C. He established the system of provinces and governance by satraps that we saw in Esther. Cyrus was broadminded for the time and allowed the far provinces of his empire to retain their cultural and religious identities. We see this at the beginning of Chapter 1:1-4.',
