        item2: 'The foretelling of Cyrus by name makes it clear to anyone reading this passage in conjunction with the Book of Ezra that God is in control of history. The Jewish historian Josephus wrote that Cyrus read the prophetic passage in Isaiah concerning him, and was eager to fulfill what God had spoken.'
