    paragraph2: 'One of the splendid narratives in the Book of Esther, which also continues in Ezra, is the use of official Persian court documents and statements. Here it is again beginning in verse 2. J. Brian Tucker notes that the language of this edict is quite close to the wording of the Elephantine papyrus, dating back to 407 B.C., which requested permission from the Persian provincial governor to rebuild a temple in the Egyptian city of Elephantine.',
    quest2: 'Read the proclamation. What do Cyrus and God intend to accomplish?',
