    content: 'Cyrus began his rule of the greater Persian Empire in 538 B.C. He established the system of provinces and governance by satraps that we saw in Esther. Cyrus was broadminded for the time and allowed the far provinces of his empire to retain their cultural and religious identities. We see this at the beginning of Chapter 1:1-4. In contrast to the Book of Esther, Ezra is rich with references to God. We begin with a proclamation from Cyrus in verse 2 but there is more. Verse 1 shares much information about how God is working in these circumstances. Question: What do we learn? God stirred Cyrus’ spirit causing him to issue his proclamation and to put it into writing, showing us that God is not limited to those who follow him. All of creation is subject to him; and no one is beyond his influence. What is stunning is that Cyrus’ role was prophesied by Isaiah around 680 B.C., more than 140 years before Cyrus issued his edict in 538 B.C. Isaiah 44 and 45 prophesy both Christ and Cyrus. Cyrus is identified, by name, as the one who will be God’s instrument in delivering Israel from the Babylonian captivity. Beginning in Isaiah 44:24 God says, “ “I am the LORD who made all things, who alone stretched out the heavens, who spread out the earth by myself,…who says of Cyrus, ‘He is my shepherd, and he shall fulfill all my purpose; saying of Jerusalem, ‘She shall be built,’ and of the temple, ‘Your foundation shall be laid.” The foretelling of Cyrus by name makes it clear to anyone reading this passage in conjunction with the Book of Ezra that God is in control of history. The Jewish historian Josephus wrote that Cyrus read the prophetic passage in Isaiah concerning him, and was eager to fulfill what God had spoken. Assuming that Ezra is the primary writer of the Book of Ezra what does he know that enables him to have this bold insight to Cyrus’ motivation? Ezra led a second group of exiles to Israel in 458 B.C. Esther and Mordecai followed God’s leading to deliver the Jews from extermination in 473 B.C., and after this Purim was celebrated every year in remembrance. Ezra was alive during this time and would have been witness to the events that unfolded in the Book of Esther. He had seen God at work. Cyrus’ proclamation itself also aligns with the prophecy in Jeremiah concerning the length of Israel’s captivity. Here is what Jeremiah 29: 10-13 says: “(10) This is what the LORD says: “When seventy years are completed for Babylon, I will come to you and fulfill my good promise to bring you back to this place. (11) For I know the plans I have for you,” declares the LORD, “plans to prosper you and not to harm you, plans to give you hope and a future. (12) Then you will call on me and come and pray to me, and I will listen to you. (13) You will seek me and find me when you seek me with all your heart. (14) I will be found by you,” declares the LORD, “and will bring you back from captivity.[b] I will gather you from all the nations and places where I have banished you,” declares the LORD, “and will bring you back to the place from which I carried you into exile.” The length of time is calculated from the time the Babylonians first invaded Israel in 606 B.C. until the first exiles returned to Judah, in approximately 506 B.C.; this is indeed 70 years. Scholars argue over the significance and application of the 70-year span, but the fact that it applies so clearly as stated above is supernatural in its correspondence. One of the splendid narratives in the Book of Esther, which also continues in Ezra, is the use of official Persian court documents and statements. Here it is again beginning in verse 2. J. Brian Tucker notes that the language of this edict is quite close to the wording of the Elephantine papyrus, dating back to 407 B.C., which requested permission from the Persian provincial governor to rebuild a temple in the Egyptian city of Elephantine. Question: Read the proclamation. What do Cyrus and God intend to accomplish? For Cyrus it was part of a larger and brilliant political strategy to win the allegiance of all the provinces in his vast and new empire by allowing them to keep their unique cultural and religious identities. Cyrus openly recognized the Hebrew God, identifying him as “the LORD God of heaven”, a title that is used 17 times in Ezra, Nehemiah, and Daniel (Warren Wiersbe, Be Heroic). God uses Cyrus’ words and formal edict to tell us that he is at work. God calls on a substantial number of people to accomplish his purpose and he gives them a singular mission to accomplish that supersedes their individual interests and desires. Cyrus intends to create a formal record of his rulings and decrees as king, so that there can be no confusion as to his decision. God uses it to create a record of his purpose and will, that we as believers may read today as proof of his larger plan across history, and his continuing preparation for the arrival of Christ. Following this decree there is an increase and broadening of activity as preparation is made for the return to Jerusalem. Ezra tells us in verse five that God also prompted the spirits of those Jews who he wanted to participate in the rebuilding of the Temple. This also means that many Jews are not prompted to return (think of Esther and Mordecai). We learn in verse six that “…all those who were about them” or “their neighbors” assisted in providing silver, gold, livestock, and goods. This means that the Persians themselves help fund the venture, just as the Egyptians had given the Israelites vast material help as they left Egypt for the Promised Land. In addition, Cyrus had Mithredath, his treasurer, return all of the Temple objects that Nebuchadnezzar, the Babylonian king, who had sacked the Temple and carried away all of its artifacts, placing them in the temple of his god. He hands them to Sheshbazzar, called the prince of Judah, in verse eight. Sheshbazzar and Zerubbabel are both identified as leaders of the exiled Jews and their relationship is unclear from scripture. It’s been suggested that Sheshbazzar is Zerubbabel, one man having two names, one Persian and one Hebrew, just like Esther, who was also Hadassah. Another suggestion is that Sheshbazzar was the court official the Persians held officially accountable for the return to Jerusalem and the reconstruction of the Temple, while Zerubbabel actually led and accomplished the work. Zerubbabel is named in both genealogies concerning Christ, which appear in Matthew and Luke. After this, running to verse 70 at the end of chapter 2, we get a meticulous listing of Temple artifacts and a long listing of the people and families returning to Jerusalem. People are identified as leaders and tribal leaders, by family name and geographic place name, as priests, as Temple workers and Temple servants (Levites, singers, and gatekeepers), descendants of servants to Solomon, and then those who claimed to be either priests or descendants of Jewish families but could not prove their ancestry. Altogether, including people not explicitly mentioned, they number are about 50,000 including servants. Question: When we encounter genealogies in scripture, they can seem pedantic and superfluous. But there are substantial reasons for their inclusion, and this is reinforced by its inclusion in Nehemiah as well. Why do God and Ezra focus on the genealogy in this narrative? The Jews had been in exile for more than two generations and away from the Promised Land. Two generations was long enough to sweep away remembrance of the Law and past practices for many Israelites. This is God’s way of intentionally reconstituting a segment of his people. It created and preserved a clear written record identifying the people who rebuilt and reestablished the Temple; so there could be no future claims that Persians or Samaritans rebuilt the temple or that any different use was intended for the Temple. It tells us that God remembers each of us individually and remembers us by name. It confirms the prophesy in Isaiah 44 and 45 that Cyrus the Great would be God’s instrument in delivering the Israelites from the Babylonian captivity and would play a role in God’s greater plan to bring Christ into the world. '
