    content: 'The 900-mile journey back to the province of Judah and the city of Jerusalem would have taken nearly four months. The journey is not described in scripture. The trip back to Jerusalem was just the beginning. Another journey, even harder, awaited them in Judah itself. Much had changed between the destruction of the Temple in 587 B.C. Remember that Nebuzaradan, general to Nebuchadnezzar, the Babylonian kin who conquered Judah, had led forces into Jerusalem and destroyed both the Temple and the city itself at length. All of the leaders, wealthy families, scribes, and best craftsmen were taken into exile. Many of the remaining people had already fled before the siege. Jeremiah 40 and 41 tell us that Nebuchadnezzar appointed Gedaliah, a Jew, as Babylonian governor of Judah. Gedaliah’s father was Ahikam, who had been an official in the court of King Josiah, and who had convinced a very angry King Jehoiakim not to turn over the prophet Jeremiah to be murdered by the people of Judah. Since Jerusalem was destroyed Gedaliah set up a new capital city at Mizpah to the north. Gedaliah was actually a very good governor. He convinced the rest of the Jewish army to lay down their arms, restored order, gave Jeremiah protection and worked with him to encouraged many of the Jews who had fled to return and begin cultivating the land again. In the meantime another Jewish leader, Ishmael plotted with Baalis, king of the Ammonites, a neighboring people who had not yet been conquered by the Babylonians, and who were unhappy with Gedaliah’s cooperation with the Babylonians, to assassinate Gedaliah. Ismael and his followers assassinated Gedaliah and his entire staff along with his Babylonians advisors during a celebration of Rosh Hashanah in Mizpah. Many of the Jews still living in the province of Judah fled to Egypt once they heard this news, fearing reprisals by the Babylonians. Jeremiah shared God’s instruction with this remnant. God wanted them to remain in Judah. But they ignored this and fled to Egypt anyway only to perish or further dispersed when Nebuchadnezzar invaded Egypt. To the north the old province of Israel had been replaced by Samaria, a polyglot region that claimed to be practicing the Jewish faith as God’s chosen people. Question: Given these events, what conditions and challenges did Zerubbabel, the new governor, Jeshua the high priest, and the returned exiles face when they returned to Judah? Jerusalem is destroyed and abandoned, and the Temple has been obliterated. The region’s infrastructure, roads, wells, walls, grain store houses, forges, and threshing floors are overgrown and in disrepair. Whole villages are abandoned. After 50 years without leaders, priest, scribes, and prophets the few people remaining in Judah have forgotten the Law and how to keep it. In addition these remaining people would not greet the returning exiles with open arms (remember Naomi’s return to Bethlehem in Ruth). They would have begrudged sharing the land, the slim harvest, and dwindling flocks. The exiles have assumed Persian dress, accents, and customs without even being fully conscious of it and would have appeared exotic and foreign to the inhabitants of Judah. Fields are uncultivated, and grain stores are few. What will they eat for the first year? Samaritans, Ammonites, Idumeans, Philistines, Egyptians, and other regional peoples would have drifted into the boundaries of Judah and laid claim to land, shelter, and fields. Many of the returning exiles were urban dwellers in Persia. The new life would have been very, very hard. The first report upon the return to Judah is about giving and location. Family and tribe leaders are inspired to add to the collection they have been gathering to pay for the rebuilding of the Temple. Verse 70 tells us that families, priests, Levites, gatekeepers, singers, and temple servants all returned to the locations they had previously occupied in Judah. This tells us two things, first that the commitment to rebuilding the Temple is the first priority, and, second, that the return of the people to the geographic locations God had assigned them in the Promised Land is also important as it begins to reestablish the character and boundaries of Judah. Question: What actions do the Jews take to declare their identity and their relationship with God? Why is this important? They build an altar to offer burnt offerings according to the Law of Moses, on the site of the ruined Temple. This act was greeted with hostility and opposition by people who were in the land, Jew and Gentile, when the exiles returned. Verse 3 tells us that fear was on the exiles “because of the peoples of the lands.” This means that the exiles were enormously uncomfortable in reestablishing the Law as prescribed by God and Moses, but were determined to do so. They began the regular and consistent practice of offering sacrifices and kept one of the great Jewish festivals, the Feast of Booths, which celebrates God’s provision of the harvest. Verse 8 speaks to the passage of another seven months while the exiles gathered material and appointed workers for the rebuilding. In verse 10 they finally made the first attempt to lay the foundation of the temple of the LORD. Then they marked this accomplishment by celebrating and giving thanks to God. Two things stand out. First, the exiles are unified and working together. This was imperfect, and it did not last. In 520 B.C., 17 years later, God would use the prophet Haggai to rebuke the returned exiles for spending more time, money, and material on rebuilding their homes than on the Temple. But in chapter 3 we see a clear commitment to being a unified community of faith. Second, the returned exiles are determined to stay very near to God. They take risks to be faithful: renewing the faithful sacrifices under the Law despite hostility, focusing on the Temple first even though food and shelter are primary concerns, celebrating God’s provision and the setting of the Temple foundation, even though this means delay in further construction. The celebration itself is poignant. Many rejoice at the laying of the foundation. But the old men who remembered the vast majesty of Solomon’s temple are moved to tears of sadness and loss, and their lamenting is as loud as the celebrating. Perhaps some of their sadness is in remembering the stark and continuous disobedience that led to the loss of the Temple. In chapter 4 a stronger, more concerted, and organized opposition arises to the rebuilding of the Temple. Question: This opposition teaches us about what we might experience when God calls on us to accomplish his purposes and the dangers of focusing too much on worldly resistance. What do we learn? First the Samaritans to the north offer to help rebuild the Temple. It’s not a well-intended offer. Samaria occupied the old boundaries of the northern kingdom of Israel. It had been deliberately resettled by Assyrian kings, beginning with Sargon II, with other peoples from the Assyrian Empire, including people from Babylon, Cuthah, Avva, Hamath, and Seharvaim, replacing the Israelites (see 2 Kings chapter 17:24-40). These new settlers worshiped a wide range of lesser gods and tried to incorporate the worship of the God of Israel along with this. They had no interest in seeing the Temple rebuilt or Jerusalem restored. Both Judah and Samaria were part of one Persian province called “Beyond the River.” The center of power and administration for this province was not in Judah but in Samaria. Mizpah was the center of government and Bethel had replace Jerusalem and the Temple as the religious center. The Samaritans and their Persian allies wanted to keep it that way. The governor of Judah, Zerubbabel, decisively rejected the false proffer of assistance. There was also a very wise administrative reason to reject the Samaritans; Cyrus the Great’s edict granted the right to rebuild to the returning exiles, not to the Samaritans. Verse 4 is important. Political operatives from the province bribed advisors and leaders in the province, and perhaps in the Persian court as well, to speak against the reconstruction of the Temple. Facing this, the returned exiles lost their focus and abandoned their trust in God. They became discouraged and then afraid. Remember, Cyrus the Great had given the exiles his backing and had not withdrawn it. God stirred Cyrus. The exiles knew this but gave up anyway. Instead they fell prey to what Haggai convicts them for. They focused on building their own dwelling places and making themselves comfortable. Efforts to reconstruct the Temple ceased around 536 B.C. and didn’t resume again until 520 B.C. The opposition was skillful and continued over time. The narrative leaps far ahead to share a particularly powerful example of this. Beginning in verse 7 we see a beautifully crafted letter from the leadership of the province condemning the rebuilding of Jerusalem (not the Temple). The letter was written to king Artaxerxes who ruled from 464 B.C. to 423 B.C. Artaxerxes, who had not issued an edict permitting the general rebuilding of Jerusalem, was persuaded by the letter and ordered efforts to rebuild Jerusalem to cease. In verse 23, the Samaritan controlled provincial administration used the king’s reply to enforce the end of all reconstruction, including the Temple. What this tells us is that when God, Christ, and the Holy Spirit stir us to act on their behalf there may be very strong opposition from the world. Sometimes this opposition will seem better organized and more effective than what God has given us. It’s not; but it is easy for us, as it was for the returning exiles, to take counsel of our fears and discouragement rather than standing on the promises of God. The returned exiles suspended their efforts even though God had given them a clear promise and a clear purpose. The same thing can happen to us. n Isaiah 41: 1- 4 & 8-13, God spoke to Israel, spoke to the returned exiles, and speaks to us today about this very thing: ““Be silent before me, you islands! Let the nations renew their strength! Let them come forward and speak; let us meet together at the place of judgment. (2) “Who has stirred up one from the east, calling him in righteousness to his service? He hands nations over to him and subdues kings before him. He turns them to dust with his sword, to windblown chaff with his bow. (3) He pursues them and moves on unscathed, by a path his feet have not traveled before. (4) Who has done this and carried it through, calling forth the generations from the beginning? I, the LORD—with the first of them and with the last—I am he.”...“But you, Israel, my servant, Jacob, whom I have chosen, you descendants of Abraham my friend, (9) I took you from the ends of the earth, from its farthest corners I called you. I said, ‘You are my servant’; I have chosen you and have not rejected you. 10 So do not fear, for I am with you; do not be dismayed, for I am your God. I will strengthen you and help you; I will uphold you with my righteous right hand. (11) “All who rage against you will surely be ashamed and disgraced; those who oppose you will be as nothing and perish. (12) Though you search for your enemies, you will not find them. Those who wage war against you will be as nothing at all. (13) For I am the LORD your God who takes hold of your right hand and says to you, Do not fear; I will help you.”” In this passage God tells Israel that he will stir up one from the east, meaning Cyrus the Great, and promises to Israel his own divine intervention to bring about his purposes, including the vanquishing of seemingly overwhelming enemies. Knowing this the Israelites should not fear, should not be dismayed, and should remember that God is at hand to help them. We should remember this as well.'
