    content: "Chapter 5 brings us back to the first return of the exiles under Zerubbabel and Jeshua. They ceased work on the temple beginning in 536 B.C. and it is now 520 B.C. At the beginning of chapter 5 we see that the returned exiles have drifted from their original purpose in rebuilding the temple. Cyrus the Great died in 530 B.C. His son, Cambyses II, ruled from 530 B.C. until 522 B.C. Cambyses II died under disputed circumstances, and he was succeeded by Bardiya, who was either a younger son of Cyrus the Great or an impostor who was murdered shortly after assuming the throne. Darius the Great, who will rule from 522 B.C. until 486 B.C., is now the king. At the beginning of chapter 5 we read that God moved his prophets, Haggai and Zechariah to prophesy. Here is what Haggai, in Haggai, chapter 1, said. Question: In Haggai God is speaking directly to the chosen exiles he stirred and caused to return to Judah. What is his focus? ““In the second year of Darius the king, in the sixth month, on the first day of the month, the word of the LORD came by the hand of Haggai the prophet to Zerubbabel the son of Shealtiel, governor of Judah, and to Joshua the son of Jehozadak, the high priest: (2) “Thus says the LORD of hosts: These people say the time has not yet come to rebuild the house of the LORD.” (3) Then the word of the LORD came by the hand of Haggai the prophet, (4) “Is it a time for you yourselves to dwell in your paneled houses, while this house lies in ruins? (5) Now, therefore, thus says the LORD of hosts: Consider your ways. (6) You have sown much, and harvested little. You eat, but you never have enough; you drink, but you never have your fill. You clothe yourselves, but no one is warm. And he who earns wages does so to put them into a bag with holes. (7) “Thus says the LORD of hosts: Consider your ways. (8) Go up to the hills and bring wood and build the house, that I may take pleasure in it and that I may be glorified, says the LORD. (9) You looked for much, and behold, it came to little. And when you brought it home, I blew it away. Why? declares the LORD of hosts. Because of my house that lies in ruins, while each of you busies himself with his own house. (10) Therefore the heavens above you have withheld the dew, and the earth has withheld its produce. (11) And I have called for a drought on the land and the hills, on the grain, the new wine, the oil, on what the ground brings forth, on man and beast, and on all their labors.” (12) Then Zerubbabel the son of Shealtiel, and Joshua the son of Jehozadak, the high priest, with all the remnant of the people, obeyed the voice of the LORD their God, and the words of Haggai the prophet, as the LORD their God had sent him. And the people feared the LORD. (13) Then Haggai, the messenger of the LORD, spoke to the people with the LORD's message, “I am with you, declares the LORD.” (14) And the LORD stirred up the spirit of Zerubbabel the son of Shealtiel, governor of Judah, and the spirit of Joshua the son of Jehozadak, the high priest, and the spirit of all the remnant of the people. And they came and worked on the house of the LORD of hosts, their God, (15) on the twenty-fourth day of the month, in the sixth month, in the second year of Darius the king.”” God acknowledges that people, including many of the exiles, are saying that it’s not time to rebuild the temple and have used this excuse for 16 years. He convicts the exiles of using the time, labor, and materials meant for the temple to build lavish “paneled houses” for themselves, selfishly using the cedars from Lebanon to line their dwellings. God reveals the restless hearts and souls of a people who have turned to themselves and away from God. They‘ve been busy but have accomplished very little. Nothing makes them happy; not food or drink, or clothes. Their disobedience has left them cold. God has not been able to bless the returned exiles because they have been steadfast in their disobedience. God has dried up the rain that sustains the soil, and all the essential harvests have diminished. The words of Haggai and Zechariah, God’s prophets, are effective and convicting. Verse 13 tells us that the returned exiles heard, obeyed, and rekindled their respect for God. Not only does God share clear words of revelation, conviction, and instruction, and he acts affirmatively to again stir the exiles (v. 14) to resume reconstruction of the temple. Contrast this inertia and disobedience with Esther, who also faced uncertain and threatening circumstances, and resolute enemies; but chose to obey God as best she could. When she made her choice to obey, regardless of cost, God blessed her actions. Psalm 119: 129 & 130, reminds us of the powerful guidance, positive support, conviction, wisdom, and revelation that God’s word brings to our lives: ““Your testimonies are wonderful; therefore my soul keeps them. (130) The unfolding of your words gives light; it imparts understanding to the simple.”” James 1:22-25 says: ““(22) But be doers of the word, and not hearers only, deceiving yourselves. (23) For if anyone is a hearer of the word and not a doer, he is like a man who looks intently at his natural face in a mirror. (24) For he looks at himself and goes away and at once forgets what he was like. (25) But the one who looks into the perfect law, the law of liberty, and perseveres, being no hearer who forgets but a doer who acts, he will be blessed in his doing.”” This is the expression of faith in response to God’s word. Psalm 51 tells us that the sacrifices that God values most are a broken spirit, and a broken and contrite heart. This is where real faith begins, in the absolute recognition that it is only through God that we have life and purpose. When we approach God this way he draws near to us. God’s word is effective. Ezra 5:2 tells us that Zerubbabel and Jeshua are inspired and begin to rebuild the temple. This renewal of effort brings official inquiry from the presiding governor of the province, Tattenai, and Shetharbozenai, who may have been Tattenai’s chief advisor, and other provincial administrators. They don’t oppose the construction in the same way Samaritan administrators did in the letter shared in chapter 4, but they want confirmation that the rebuilding of the temple has the king’s approval. Question: How is this different from challenge shared in chapter 4? The returned exiles don’t stop working. Tattenai is more inquiring than opposing. There is a reason for this. Verses 4 & 5 tell us:““(4) They[a] also asked them this: “What are the names of the men who are building this building?” (5) But the eye of their God was on the elders of the Jews, and they did not stop them until the report should reach Darius and then an answer be returned by letter concerning it.””God’s intersession restrains Tattenai and grants the returned exiles the courage to continue. We even see God’s strong hand in the correspondence to Darius the Great; in verses 11-17. The letter that Tattenai and his administrators write is a thing of beauty. It is an official inquiry, written in Aramaic, the official administrative language of the Persian Empire; but within its text the returned Jewish exiles bear testimony to who they are, the origin of the temple, the nature of the God they worship, their history of disobedience, God’s plan, and Cyrus the Great’s role, and his acknowledgement of the very God for whom the temple is being reconstructed. We even get details confirming that the returned exiles are the builders and the approach they are taking to accomplish the rebuilding. We also see, through the eyes of nonbelievers, the renewed faith and commitment of the returned exiles. They are accomplishing God’s purpose in rebuilding the temple, Plus the letter asks for confirmation of the original decree from Cyrus the Great, giving double confirmation, at different points in time, to the original edict. The edict that God inspired.At the beginning of chapter 6:1-12, we see that Tattenai’s letter launches a full search of the royal archives. The search expands from Babylonia proper, and travels east to the region of Media and the city of Ecbatana, which served as the summer residence of the Persian kings, starting with Cyrus the Great. In Ecbatana they find a copy of the edict issued nine years and three kings earlier. Darius’ response is electrifying. Question: How does Darius support the rebuilding of the temple? First the original decree from Cyrus the Great is restated. Remember that God stirred Cyrus to write this decree and it seems that Darius is also stirred by God once he reads it. Darius’ first command in verse 6 is for Tattanai and all of his officials to stay away from the Jews and not interfere with the rebuilding of the temple; and to permit the local Jewish govern, Zerubbabel, and the Jewish elders to rebuild the temple on its original site. Because of this instruction from the king there can be no subsequent claim that the Persians or Samaritans assisted in or oversaw the reconstruction, or that the temple was moved from its original location. The true record is established by God, through Darius, in the Book of Ezra. All expenses and any materials needed are to be supplied by the treasury of the Province Beyond the River. The provincial rulers could not claim that funding needed to come from Babylonia proper. Any revision of Darius’ edict was punishable by death; discouraging the sort of sustained lobbying we saw with the letter in chapter 4. Darius concludes by asking that God punish and overthrow any king, official, or person who seeks to overturn the edict or interfere with the reconstruction of the temple. Through Haggai God speaks to the doubts, emotions, and fears of the returned exiles: ““(3)Who is left among you who saw this house in its former glory? How do you see it now? Is it not as nothing in your eyes? (4) Yet now be strong, O Zerubbabel, declares the LORD. Be strong, O Joshua, son of Jehozadak, the high priest. Be strong, all you people of the land, declares the LORD. Work, for I am with you, declares the LORD of hosts, (5) according to the covenant that I made with you when you came out of Egypt. My Spirit remains in your midst. Fear not. (6) For thus says the LORD of hosts: Yet once more, in a little while, I will shake the heavens and the earth and the sea and the dry land. (7) And I will shake all nations, so that the treasures of all nations shall come in, and I will fill this house with glory, says the LORD of hosts. (8) The silver is mine, and the gold is mine, declares the LORD of hosts. (9) The latter glory of this house shall be greater than the former, says the LORD of hosts. And in this place I will give peace, declares the LORD of hosts.”” As the Jews rebuild in a stark landscape God shares a far greater promise: the coming downfall of Persia to Alexander the Great, and the arrival of his own son,Christ Jesus at the temple gate to bring peace and salvation to the world. Book Of Ezra: God’s Unbroken Circle; The Rise Of Ezra. Finally, on March 12, of 516 B.C. the rebuilding of the temple was completed; approximately 70 years after Nebuchadnezzar ordered its complete destruction in 586 B.C; and 21 years after Zerubbabel and Jeshua led the exiles back to Jerusalem and first laid the temple foundation. Darius the Great, the current king of Persia has set the stage by warning off all of the provincial leaders situated in Samaria, including Tattenai, the governor, to not only stay out of the way of the rebuilding but to help finance it through the provincial budget. Ezra 6:14 tells us that Haggai the prophet and Zechariah provided continuous encouragement and preaching during this time. Interestingly Ezra gives credit to God, Cyrus, Darius and Artaxerxes for completing the temple. In 516 B.C., Artaxerxes would not become king for another 50 years. This comment actually supports the identification of Ezra as author of the book of Ezra. He is giving Artaxerxes a nod of approval and appreciation for supporting Ezra’s own mission which will help rebuild the walls of Jerusalem and the city itself, extending a greater protection to the temple. This is a transitional passage. Once the temple is rebuild the narrative advances 57 years to approximately 458 B.C. during the reign of Artaxerxes and the appearance of Ezra. Question: How do the exiles celebrate the reconstruction of the temple? Keep in mind the profound importance of this moment. The temple that Solomon built lasted from approximately 957 B.C. until 586 B.C. , more than 370 years, and was the center of religious life for Israel. The destruction of the temple and the dispersing of the population brought into question the very identity of Israel, and the distinctive worship relationship with God, established by Moses, and embodied by the temple. So this moment when temple is restored, with many of its original appointments and sacred vessels, regardless of how modest, is extraordinary and profound for the nation and history itself. This new temple will last until 70 A.D., more than 580 years. This is the temple that Herod the Great would improve and enlarge, the temple where Christ would enter in. Ezra 6:16 tells us that the priest, Levites, and the rest of the exiles celebrated the dedication of the temple with joy. The reconstruction of the temple reestablishes the specialness of the Jerusalem, the Promised Land, and the people themselves which we see in in verse 19. Thousands of sacrifices had accompanied the dedication of Solomon’s temple. Here the modest number of slightly more than 700 would do; including a sin offering of 12 goats for each tribe. Significantly, the priests and Levites are formally reinstalled in the temple at this time, resuming their roles as required by, and recorded in the Book of Moses, referring to passages in Exodus, Leviticus and Numbers.Following this they celebrate Passover a month later. The celebrants are the returned exiles and those who had separated themselves from the unclean practices of their Gentile neighbors in order to seek the LORD. This is extraordinarily significant and important in reestablishing the unique nature of Israel as God’s chosen people and the renewed significance of Jerusalem. It must have been a moment of great tension as well as great joy, because the Samaritans are shut out from the position they covet, which is the formal representative of the God of Israel in addition to all of the other pagan gods they embrace and condone. It is the triumph of faith and true relationship with God over convenience. God will not allow his purpose and vision for the world to be compromised or corrupted. God doesn’t need an empire, he doesn’t need political power or influence, he doesn’t need geopolitical alliances; first and foremost God wants people who believe in him. We, as believers, need to remember this. If God had not motivated Darius to decree a zone of protection for the exiles, this moment of celebration and worship would not have occurred. In Esther we saw that God could and would bless his chosen people in their new lives as exiles. In Ezra we see the continuing importance of the temple, Jerusalem, and the place that God had set aside as the Promised Land; so significant that it will be the place that Christ enters into the world.The Book of Ezra also stands as a confirming record for succeeding generations of Jews concerning the reestablishment of God’s temple, city, and his chosen people, dwelling in the world, but set apart. God did not intend or accept that they should become like the Samaritans"
