    paragraph4: 'At the beginning of chapter 8 we are again presented with a genealogy of those who returned. Warren Wiersbe writes that “Many of the Jews were comfortable in Mesopotamia and quite satisfied to live and die there. During their captivity, they had followed Jeremiah’s counsel to be good citizens and settle down to normal lives (Jeremiah 29:1-7). Over the decades, the old generation had died and a new generation had arsine that had never seen Jerusalem or he temple and probably had little interest in the welfare of their fellow Jews laboring sacrificially there. No doubt some of the Jewish men were in government employ or in business and were unable to relocate without paying a great price. “ (Warren Wiersbe, Be Heroic, David C. Cook, Ontario.)',
