    content: "In chapter 7 we leap ahead almost 58 years, to the seventh year of the reign of King Artaxerxes, son of Xerxes. God describes Ezra with care at the beginning of chapter 7. Question: What do we learn about Ezra? First he is a priest from a family of priests descended directly from Aaron. He came out of the city of Babylon, which, according to F. Charles Fensham (The Books of Ezra and Nehemiah, Eerdmans, 1982, p. 98) was where the majority of Jews, still living in exile, were located. Nehemiah came out of Susa, home city to Esther and Mordecai. In verse 7 Ezra is described as a secretary or teacher well versed in the Law of Moses, meaning he has appointed authority from Artaxerxes, and is one of the keepers of the Law of Moses, as God had given it. This means that Ezra has a special anointing from God. Ezra and those like him are essential to reestablishing the observance of the Law as God had originally prescribed through Moses. Fensham describes ‘secretary’ as a specifically Persian office; granting Ezra secular power. We also learn, most importantly, that the hand of God is on Ezra, and because of this Ezra had the full favor and confidence of the king, and the king’s advisors. Ezra led a contingent of exiles to Jerusalem, including priests, Levites, singers, gatekeepers, and temple servants. These are people clearly intended to strengthen the work of the temple in the community that is now reviving and rising in Judah. Again in verse 9 we read that the gracious hand of God was on Ezra, because of Ezra’s own faith in and devotion to God. In verse 10 we read that even in exile, Ezra had devoted himself to the study and observance of the Law, and taught it to the exile community. Ezra is God’s man, and is committed in faith to God in the way that Moses had commanded of Israel with such direct urgency in Deuteronomy 30: 9-18. “The LORD will again delight in you and make you prosperous, just as he delighted in your ancestors, (10) if you obey the LORD your God and keep his commands and decrees that are written in this Book of the Law and turn to the LORD your God with all your heart and with all your soul. (11) Now what I am commanding you today is not too difficult for you or beyond your reach. (12) It is not up in heaven, so that you have to ask, “Who will ascend into heaven to get it and proclaim it to us so we may obey it?” (13) Nor is it beyond the sea, so that you have to ask, “Who will cross the sea to get it and proclaim it to us so we may obey it?” (14) No, the word is very near you; it is in your mouth and in your heart so you may obey it. (15) See, I set before you today life and prosperity, death and destruction. (16) For I command you today to love the LORD your God, to walk in obedience to him, and to keep his commands, decrees and laws; then you will live and increase, and the LORD your God will bless you in the land you are entering to possess. (17) But if your heart turns away and you are not obedient, and if you are drawn away to bow down to other gods and worship them, (18) I declare to you this day that you will certainly be destroyed. You will not live long in the land you are crossing the Jordan to enter and possess.””  Ezra’s faithfulness and obedience demonstrate to us what the faith of one person can accomplish. God wants us to see and know this, and live this way. Artaxerxes armed Ezra with powers and a specific mission in a remarkable letter, the text of which is fully captured in chapter 7. Ezra must have asked Artaxerxes for the grant and authority captured in this letter. Question: What does the letter provide? The letter is written in Aramaic, the administrative language of the Persian Empire. In verse 13 Artaxerxes allows other exiles, including priests and Levites, who volunteer, to go with Ezra to Jerusalem. Ezra wants only those who feel led by God to go with him. Ezra is authorized to assess whether the Law of God is being obeyed in the region of Judah and the city of Jerusalem. This fits with Ezra’s mission to teach the Law, and right obedience to the Law, and the stories that he must have heard about Samaritan and other dangerous influences in the region. Ezra is authorized to take the funding he receives to provide specifically for the worship needs of the temple. It is important to know that Ezra’s grant of authority, see verse 21, is given to him as “the priest” and “the teacher of the Law of God of heaven.” Ezra has a different kind of power and authority as the king’s secretary, but chooses to stand on his identity as God’s man in accomplishing his purposes. All of his financial dealings and requests for additional funding are focused with laser-like specificity on the needs of the temple and the temple staff. Ezra asks nothing for himself in terms of wealth, housing, material comfort, or political authority. In verse 19 Artaxerxes entrusts Ezra with the remaining temple artifacts not already returned by Zerubbabel; and in verse 24 we see Artaxerxes wise view and partial understanding of who God is. He says ‘give the God of heaven what he requires and let his temple be honored as God instructs. I don’t want this God to be angry with me or my household.’ In verse 25 Artaxerxes provides a remarkable grant of power in response to what must have been a very bold request from Ezra. He gives Ezra the power to appoint, in the king’s name, magistrates and judges to administer justice to “all who know the laws of your God,” meaning returned exiles and others who remained in Judah and kept the law. Further, Ezra is given power to teach the laws of God to “any who do not know them.” Clearly God’s hand is on Ezra and on Artaxerxes in the granting of this request. It’s openhanded wisdom, mercy, seeking of the welfare of others, and care bears God’s own character. In verse 26 Artaxerxes identifies the law of God with his own law as king. Ezra is granted the right to enforce both, and is given the power to impose death, banishment, confiscation of property, or imprisonment as punishments for disobedience. Ezra’s response in verses 27 & 28 is wonderful instruction for us today. He does not glory in his new powers or worry about the challenges. He praises God for influencing Artaxerxes to bring honor to God’s house in Jerusalem, and for granting his divine favor to Ezra. Ezra is wise and selfless. He says that because God is with him, and only because God is with him, he has the courage to gather other exiled leaders to go with him to Jerusalem. Ezra knows that God is the source of all he is, has, and may do. Chapter 8: Journey, Example, and Challenge. The assembly of exiles returning to Jerusalem is much smaller this time. Not nearly as large as the assembly Zerubbabel, Sheshbazzar, and Jeshua led, nearly 50,000, in 537 BC. This group, in 458 B.C. numbered around 1500, and may have approached 5000 after the priests and temple servants were added, and women and children are counted as well. The narrative form also changes. It moves from third person, to first person, Ezra sharing the account in his own voice. In addition, in the first return in 537 B.C., there was no focus on the journey back. This time we get a much more detailed account. Question: What is God trying to accomplish by changing the voice of this narrative to first person? Faith begins with the personal and individual, the relationship between one person and God. God lets us see inside of this relationship with Ezra, which is important, because that is where we begin every day. Ezra becomes us rather than a distant historical figure. Faith has a strong emotional component. We hate to admit fear, or shame, or weakness, or doubt, or anger, but this is part of the faith journey. God always knows the worst about us and still loves us; and can still use us in his work if we look to him. At the beginning of chapter 8 we are again presented with a genealogy of those who returned. Warren Wiersbe writes that “Many of the Jews were comfortable in Mesopotamia and quite satisfied to live and die there. During their captivity, they had followed Jeremiah’s counsel to be good citizens and settle down to normal lives (Jeremiah 29:1-7). Over the decades, the old generation had died and a new generation had arsine that had never seen Jerusalem or he temple and probably had little interest in the welfare of their fellow Jews laboring sacrificially there. No doubt some of the Jewish men were in government employ or in business and were unable to relocate without paying a great price. “ (Warren Wiersbe, Be Heroic, David C. Cook, Ontario.) There is a much greater focus on the journey itself. It took four months for them to cover the 900 miles. Question: What do we learn from Ezra’s journey that helps us on our own faith walk? It takes courage and God gives us that courage; and we should both acknowledge this and lean on it. At the end of chapter 7, Ezra says that “Because the hand of the LORD my God was on me, I took courage and gathered leading men from Israel to go up with me.” Ezra knew it would not be easy to convince people to go with him but that God would make it possible, by stirring the hearts of those he wanted to make the journey. The journey of faith is not perfect, and we should expect this. It doesn’t mean that God has abandoned us when there are failures, challenges, or glitches. Ezra assembles his travelers at the canal that flows towards Ahava and discovers that he has no Levites. The Levites were from the tribe of Levi and were men set apart, as clergy and servants, to do the administrative work of the temple. They served as temple guards, gatekeepers, and temple musician. They handled the special vessels in the temple and guarded the temple treasury. They also served as judges. They did not act as priests. Ezra needed them in order to ensure that the temple was running as God intended. Apparently God has placed on Ezra’s heart a concern that the last exiles who returned to Judah and Jerusalem are not being faithful in their actions. We will see that Ezra is exactly right in chapter 9. Ezra sends a delegation to a place called Casiphia (meaning ‘silvermith’), perhaps a location where Levites lived in Babylon. Ezra instructs the delegation to speak to the head of the Levites, a man named Iddo. 38 Levites and 220 temple slaves agree to go with Ezra. Ezra recognizes who made this possible. In verse 18 he says, ““And by the good hand of our God on us, they brought us a man of discretion, of the sons of Mahli the son of Levi, son of Israel, namely Sherebiah with his sons and kinsmen…”” We need to continuously pause and worship God and seek his guidance as we work; and especially in challenging and hectic times. Ezra has the assembled traveling party observe a time of fasting and worship to seek safe journey “for ourselves, our children, and all our goods.” Like Abraham rejecting the plunder obtained by men, Ezra has rejected the possibility of a military escort; telling Artaxerxes that they would rely on the hand of God to protect their journey. Together, everyone prays for God’s protection and invests time in the process of prayer and worship. Ezra values others as God values them; he sees others as God sees them; and he trusts those that God trusts. We need to do the same. After he weights out and accounts for the 25 tons of precious artifacts, gold, and silver he entrusts the priests to guard it with these words beginning in verse 28, ““You are holy to the LORD, and the vessels are holy, and the silver and the gold are a freewill offering to the LORD, the God of your fathers. (29) Guard them and keep them until you weigh them before the chief priests and the Levites and the heads of fathers' houses in Israel at Jerusalem, within the chambers of the house of the LORD.” (30) So the priests and the Levites took over the weight of the silver and the gold and the vessels, to bring them to Jerusalem, to the house of our God.”” Ezra mirrors God’s own view and so should we. As the returned exiles were completing the temple in 516 B.C. the prophet Zephaniah reminded them of God’s active love and regard for his people. In Zephaniah 3:17 he wrote, ““The LORD your God is in your midst, a mighty one who will save; he will rejoice over you with gladness; he will quiet you by his love; he will exult over you with loud singing.”” Ezra is consistent in demonstrating faithfulness. It’s not a one-time thing. When they arrive in Jerusalem four month later they take three days to rest and reflect on how the hand of God was on them for the entire 900 mile trip and protected them “from enemies and bandits along the way.”(v. 31). Ezra seeks no personal gain for himself and demonstrates his own trustworthiness. When he arrives in Jerusalem he again weighs out the large treasure of silver, gold, and valuable artifacts to demonstrate that it is all there and accounted for. Ezra honors the commitments he has made to others, even to a pagan king like Artaxerxes. When he returns to Jerusalem he fulfills the instruction in Artaxerxes’ letter (7:17) to offer appropriate sacrifices to God on the altar temple. Artaxerxes was avid for this offering to protect himself and his court from the possible wrath of the God of Israel. This occurs in 8:35; and Ezra turns it into an act of worship. Ezra is humble and self-effacing. He could have exerted the office of king’s favorite when he shows up in Jerusalem, but, with great temperance, he shares Artaxerxes’ letter, in v. 36, with the satraps and provincial governors throughout the region; extending this sharing with officials beyond the province of Trans-Euphrates itself so that everyone will know what Artaxerxes and the God of Israel have commanded. Ezra sets a greater example for everyone."
