    content: 'Warren Wiersbe writes that “38 individual workers are named in chapter 3, and forty-two different groups are identified…” (Warren Wiersbe, Be Determined (Ontario: David Cook 2009), 29. Chapter 3 gives us a broad and detailed description of the wall rebuilding project. Question: What are some key learnings for us? First Nehemiah is the writer of the account and God’s leader for this endeavor, but Nehemiah is painstaking and scrupulous in giving others the credit, and not mentioning himself in this chapter. Second Eliashib, the high priest, and the other priests worked shoulder to shoulder with the other workers in helping with the rebuilding. Eliashib is the grandson of Jeshua, the high priest who accompanied Zerubbabel and the first large group of returning exiles in 537 B.C. The priests rebuilt the Sheep Gate on the northern side of the city. Eliashib will prove himself unworthy, but, in chapter 3, joins Nehemiah in setting a good example. The city defined by the rebuilt walls is much smaller than the city destroyed by the Babylonians. It follows the city limits that David knew. The project, as described in chapter 3, begins in the north and runs counterclockwise. Next to the priests were the men of Jericho who rebuilt the adjoining section. This signifies that returned exiles from other townships pitched-in to assist. Jericho has special significance because it was the first fortified city taken by Joshua and the Israelites when they crossed over into the Promised Land. The Sheep Gate may also have been the entrance for the road running from Jericho to Jerusalem. There is a special emphasis on describing the rebuilding of each gate. Nehemiah describes 11 gates. The gates were vital to the life of the city. Markets, justice, and business were conducted at most gates. Verse 5 tells us that not everyone agreed to help. Workers from Tekoa, a town 11 miles south of Jerusalem, helped in the rebuilding, but their leaders, perhaps local magistrates, military leaders, or scribes refused to participate. Men from locations to the north of Jerusalem, Mizpah (a seat of power once Jerusalem was destroyed) and Gibeon, also helped. This was a cooperative venture. God gave Nehemiah the vision but it took the work of many hands to make it a reality. Different guilds participated, including the goldsmiths, the perfume-makers, and the merchants; also administrators from the different regions in Judah. What is noteworthy is that these are skill-specific crafts and professions were not normally associated with large scale, brute building projects. In this case these men made mud brick, carried stone, and stood on makeshift scaffolding along with everyone else. For many of these workers the rebuilding was close to home. Verse 28 tells us that priests repairing the walls above the Horse Gate were working in an area opposite the houses that they lived in. This project must have brought the people together in a way that few things did. It was single-minded and intensive; completed in 52 days; a miracle of construction and engineering. This unity of work and purpose were intentional parts of God’s greater plan. The rebuilt walls were the substantial, physical manifestation of Ezra’s desire to reestablish Jerusalem as the center for worship and the keeping of the Law.'
