    content: 'F. Charles Fensham (The Books of Ezra and Nehemiah, p. 179) that in the Hebrew Bible the first 23 verses of chapter four are a continuation of chapter three, making the reaction of Nehemiah’s enemies more directly tied to the rebuilding of the wall. When Sanballat, governor of Trans-Eurphrates, hears that Nehemiah has begun reconstruction of the Jerusalem wall his anger explodes. Question: Sanballat’s reaction is the same reaction we often face as Christ’s church today. What is it? Beginning in verse four we see Nehemiah’s own reaction, which is important for us to understand. How does Nehemiah respond? Sanballat goes back to the first approach he used in Nehemiah 3:10, mockery and scorn, because these are so powerful. And, in verse two he makes this mockery a public and broad undertaking by doing it in front of his fellow administrators and the Samaritan army, which is the militia based defensive force for the province. He knows that all these people are connected by kinship, friendship, and business relationships with the various Jewish families helping to rebuild the wall. Some of these Jewish families are also engaged in undermining the reconstruction. Sanballat is using the ancient version of publicity. Sanballat attacks the Jews personally, calling them feeble. He attacks their project: “Will they restore their wall?” He attacks their God as ineffective: “Will they offer sacrifices?” He exaggerates and ridicules their ambitions: “Will they finish in a day?” He observes that the rubble is insufficient to build anything; knowing that all this will get back to the Jews. Sanballat’s toady, Tobiah the Ammonite, “who was at his side” joins in the mockery: “What they are building –even if a fox climbed up on it, he would break down their wall of stones!” So Sanballat uses public mockery, personal attack, distortion of facts, and appeals to sympathetic audiences as his primary instruments to derail work on the wall. All of these attacks come directly to Nehemiah. Nehemiah must have had a steady stream of people approaching him each day, and saying “guess what I heard?” And other people saying, “this task is impossible,” or “let’s wait for a better moment.” Nehemiah must have felt some anger, disappointment, and increasing impatience. He probably wanted to lash out, and hurl counter insults at Sanballat and his cronies; but his first decision was to pray to God. This choice is a life choice for Nehemiah. It was prayer and reliance on God that got him to Jerusalem, an no matter who he feels he will place God at the center. We should too. Jeremiah 29:13 says, “ You will seek me and find me, when you seek me with all your heart.” We see that Nehemiah, with God’s help, has made this part of his faith walk with God. This orientation means that Nehemiah doesn’t put himself first. His petition begins with the recognition that it is God himself and the Jews who are being mocked. His request is that God hold the Samaritans and their allies accountable for their mockery. Nehemiah trusts God’s hand to administer justice, not his own. He may have been remembering Psalms 1 which says: “Blessed is the man who walks not in the counsel of the wicked, nor stands in the way of sinners, nor sits in the seat of scoffers; (2) but his delight is in the law[b] of the LORD, and on his law he meditates day and night. (3) He is like a tree planted by streams of water that yields its fruit in its season, and its leaf does not wither. In all that he does, he prospers. (4) The wicked are not so, but are like chaff that the wind drives away. (5) Therefore the wicked will not stand in the judgment, nor sinners in the congregation of the righteous; (6) for the LORD knows the way of the righteous, but the way of the wicked will perish.” In the next verse, verse 6, Nehemiah tells us that after the onset of this campaign of jeering and mockery the Israelites were able to rebuild the wall to half its height, because “the people worked with all their heart.” This is a direct message to us, telling us to follow Nehemiah’s blue print. When under attack, when mocked, when subject to slander and distortion, turn first to Christ and God and seek their intervention and justice; let Christ define your behavior, your action, and your well-being. This approach enables us to endure the greatest stress, storms, and opposition. Nehemiah’s faithfulness does not end the conflict. When Sanballat hears that the work continues, he ratchets up the pressure and opposition. Verse seven tells us that when he hears that the repairs have gone ahead, he rallies opposition from all quarters: The Ammonites to the east, the Arabs to the south, the men of Ashdod to the west, and the Samaritans to the north. Judah was cut off. Not only was Judah surrounded by hostile camps, but Sanballat and his confederates also threatened an attack on Jerusalem itself. The stakes are higher. Nehemiah’s response to this is to have the people joined in continual prayer to God, and to post a guard day and night. This is a sober and wise response. The first emphasis is on prayer, the second on action guided by that prayer. As Nehemiah leads his people in a time of great challenge and opposition he is also setting an example that is direct testimony as to what it means to belong to God, and to exemplify God’s character, the sort of example that Paul identified in Colossians 4: “(2) Continue steadfastly in prayer, being watchful in it with thanksgiving. (3) At the same time, pray also for us, that God may open to us a door for the word, to declare the mystery of Christ, on account of which I am in prison— (4) that I may make it clear, which is how I ought to speak. (5) Walk in wisdom toward outsiders, making the best use of the time. (6) Let your speech always be gracious, seasoned with salt, so that you may know how you ought to answer each person.” Also Ephesian 6:18, “And pray in the Spirit on all occasions with all kinds of prayers and requests. With this in mind be alert and always keep on praying for all the saints.” The next challenge is the hardest one of all, and that is internal discouragement. This is very much a common challenge for us as well, and enormously important for us to understand. Question: In what ways do the Israelites demonstrate discouragement? What do we learn from this and what is Nehemiah’s reaction? First, in verse 10, there is a general murmuring across the entire province of Judah that the workers in Jerusalem are giving out, and the rubble is too vast to clear away. In other words the task is impossible there aren’t enough people to do it, and we are failing. The scripture’s sense is that these sentiments were repeated over and over again. Then the Jews living close to the borders to Judah, abutting the regions where the opposition lived, kept coming to Jerusalem and warning Nehemiah and his workers that they would be attacked and killed at any time. In verse 12 Nehemiah describes this as being told “ten times over….” Remember that this wide spread fear, anxiety, forecasting of failure and disaster, and naysaying was going on at the same time Nehemiah was praying to God himself, and had his workers praying to God around the clock. So what we learn is that even as we seek faithful and prayerful purpose, there will be doubt, fear, confusion, and anxiety about where things are headed. This should be expected and not cause us to waiver if we have prayerfully sought God’s guidance. Nehemiah addressed this in several ways. First he stationed armed guards along the wall where everyone could see them. Then Nehemiah encouraged everyone: Nehemiah used encouragement rather than criticism. He invoked God rather than his own authority, and he reminded the Israelites that they shared a common identity in God, and so had a divine and sacred accountability to fight for one another. These verses remind us of Christ’s own words concerning fear in Matthew 10, beginning in verse 26. We are not to fear those who seek our harm or to intimidate; rather we are to fear only God who has the power of life and death and the power to save or condemn our very souls. For us fear of the Lord begins with an understanding of his love and compassion and protection; his great regard for us. If we believe in Christ as the Son of God, then God will never be separated from us, regardless of peril: “(26) So have no fear of them, for nothing is covered that will not be revealed, or hidden that will not be known. (27) What I tell you in the dark, say in the light, and what you hear whispered, proclaim on the housetops. (28) And do not fear those who kill the body but cannot kill the soul. Rather fear him who can destroy both soul and body in hell. (29) Are not two sparrows sold for a penny? And not one of them will fall to the ground apart from your Father. (30) But even the hairs of your head are all numbered. (31) Fear not, therefore; you are of more value than many sparrows. (32) So everyone who acknowledges me before men, I also will acknowledge before my Father who is in heaven, (33) but whoever denies me before men, I also will deny before my Father who is in heaven.” Nehemiah had these words from Isaiah 41 which may have convicted him and certainly gave him profound guidance and comfort: “But you, Israel, my servant, Jacob, whom I have chosen, the offspring of Abraham, my friend; (9) you whom I took from the ends of the earth, and called from its farthest corners, saying to you, “You are my servant, I have chosen you and not cast you off”; (10) fear not, for I am with you; be not dismayed, for I am your God; I will strengthen you, I will help you, I will uphold you with my righteous right hand. (11) Behold, all who are incensed against you shall be put to shame and confounded; those who strive against you shall be as nothing and shall perish. (12) You shall seek those who contend with you, but you shall not find them; those who war against you shall be as nothing at all. (13) For I, the LORD your God, hold your right hand; it is I who say to you, “Fear not, I am the one who helps you.'
