    content: "As Nehemiah spent huge energy fending off challenges from his enemies, and fear and doubt from his fellow Jews, he encountered another circumstance that infuriated him and must have caused him to reconsider his whole mission. In chapter 5 he undercovers both a foundational distress and a monstrous selfishness that threatens to undermine everything. Warren Wiersbe (Be Determined: Nehemiah, David Cook, 2009, eBook) writes, “Selfishness means putting myself at the center of everything and insisting on getting what I want when I want it. It means exploiting others so I can be happy and taking advantage of them just so I can have my own way. It is not only wanting my own way, but expecting everybody else to want my way too. Why are selfish people so miserable? I think Thomas Merton said it best: “To consider persons and events and situations only in the light of their effect upon myself is to live on the doorstep of hell.” Verse 1 tells us that the people are in serious trouble and deeply divided with fellow Jews who are taking advantage of difficult circumstances. There are several urgent problems. First there isn’t enough to eat. Labor demands for rebuilding the walls of Jerusalem and maintaining security meant that farmers and vineyard workers did not have time to keep up the surrounding agricultural fields and vineyards, lessening the harvest and making Judah more vulnerable to famine. Sanballat had also cut off most of the trade routes in and out of Judah so there was no consistent way to export goods and import grain. The families that had returned from exile had also grown in size and there were more mouths to feed. Second those who had property had to mortgage fields, vineyards, and houses in order to pay for grain. Third, they still had to pay the ground tax to their Persian overlords. Darius levied an ongoing tax on all fields and vineyards that produced a crop. Fourth, children of the debtors, and those who were hungry, were being forced into servitude, slavery, by their creditors; a servitude that did not end until the debt was paid. In this case the creditors were fellow Jews who had money; Jews that Nehemiah described as ‘nobles and officials’. We may presume from what follows that these same wealthy Jews were the mortgage holders for the farmers and other landowners who have had to mortgage their fields, vineyards, and houses. In verse 5 the people complain that they are helpless as their sons and daughters are forced into service, and in some cases their daughters become second wives to wealthy Jews. They have also lost ownership of their fields and vineyards. This practice is loathsome to God and against Jewish Law. Jewish law provides that every seven years all hired servants are required to be released in order to avoid the creation of a ‘slave’ class of Jews. Question: What is Nehemiah’s reaction to this and what do we learn? He is furious but slow to anger. In verse 6 Nehemiah tells us that he is very angry, but he doesn’t give in to this. Verse 7 tells us that he “took counsel of himself” meaning that he took time to pray and reflect before acting. This process of prayer and reflection is consistent for Nehemiah throughout his account. It’s a behavior he leans on and models for everyone else around him. Here is an extremely important point for us to remember. Because Nehemiah submits his feelings to God he is able to exercise a greater influence in solving the contentious problems that confront him. Proverbs 16: 3,6-8, & 32 say, “(3) Commit your work to the LORD, and your plans will be established…(6) By steadfast love and faithfulness iniquity is atoned for, and by the fear of the LORD one turns away from evil. (7) When a man's ways please the LORD, he makes even his enemies to be at peace with him. (8) Better is a little with righteousness than great revenues with injustice…(32) Whoever is slow to anger is better than the mighty, and he who rules his spirit than he who takes a city.” Nehemiah is fearless but modest in diagnosing the problems, taking on the most powerful members of the exile community and indicting them,  “You are exacting interest, each from his brother.” He speaks plainly and doesn’t exaggerate. It may have been that he met with them privately first and they did not respond. Then, like Ezra, he realized that resolution needed to be accomplished in the open, in a moment shared by the entire returned exile community. This took great courage and selflessness for Nehemiah. He was putting his own power and authority at risk by doing this. In this large open meeting Nehemiah addressed the offenders who would have been arrayed in positions of favor closest to Nehemiah. Nehemiah told them that all the Jews, working together, had striven to buy back other Jews who were held in servitude by the Samaritans, Arabs, Ammonites, and the men of Ashdod to the east, all the surrounding pagan nations; only to have them resold into slavery by their own people. In doing this Nehemiah appealed to unity, repeatedly using the words “brother” or “brothers. The nobles and the officials were stunned and silenced by this, “and could not find a work to say.” Their silence was their first public admission of guilt. Nehemiah had more to say. From this specific accusation he moved on to appeal to a larger purpose. As he looked at the nobles and officials, and then out across the great crowd he had assembled he said, “The thing you are doing is not good. Ought you not walk in the fear of our God to prevent the taunts of the nations of our enemies?” What does it mean to walk in the fear of the Lord? It is an attitude of respect, reverence, wonder, awe, gratefulness toward God; and an understanding of his majestic power, depthless mercy and love, and perfect nature, which we are to worship, pursue, and exemplify. Psalm 111 captures this perfectly: “Praise the LORD! I will give thanks to the LORD with my whole heart, in the company of the upright, in the congregation. (2) Great are the works of the LORD, studied by all who delight in them. (3) Full of splendor and majesty is his work, and his righteousness endures forever. (4) He has caused his wondrous works to be remembered; the LORD is gracious and merciful. (5) He provides food for those who fear him; he remembers his covenant forever. (6) He has shown his people the power of his works, in giving them the inheritance of the nations. (7) The works of his hands are faithful and just; all his precepts are trustworthy; (8) they are established forever and ever, to be performed with faithfulness and uprightness. (9) He sent redemption to his people; he has commanded his covenant forever. Holy and awesome is his name! (10) The fear of the LORD is the beginning of wisdom; all those who practice it have a good understanding. His praise endures forever!” Nehemiah shares a vision of God and faithfulness that all of Israel, and we today, should aspire to. It is unifying, inspiring, and elevating. It states our true and intended identity as God’s children and emissaries. To step away from this, to turn away from God, and behave with corrupt self-interest means that Judah is no better that the surrounding pagan nations and deserves the taunts that chase hypocrisy. Nehemiah’s charge was ‘be better than this’; to be God’s people, in both thought and act. Again, Nehemiah did not appeal to his own power or stature as governor of Judah, but to God’s stature. Nehemiah followed this admonition by setting a powerful example himself. He admited that he and his closest confederates have also leant money and grain. He did not say that he and his confederates were charging interest or imposing debt slavery. He did say that they would forgive and erase all of the loans. Nehemiah acted sacrificially to set the right example. Micah 6: 6-8 says, “(6) With what shall I come before the LORD, and bow myself before God on high? Shall I come before him with burnt offerings, with calves a year old? (7) Will the LORD be pleased with[a] thousands of rams, with ten thousands of rivers of oil? Shall I give my firstborn for my transgression, the fruit of my body for the sin of my soul?” (8) He has told you, O man, what is good; and what does the LORD require of you but to do justice, and to love kindness, and to walk humbly with your God?” By this sacrificial gesture, Nehemiah exhibits God’s justice and loving kindness; and he models humility in his walk with the Lord. It is only after setting this example, and acting with utter integrity and honesty that Nehemiah called on the nobles and officials to return ownership of all fields, vineyards, and houses, and to cease the interest charges that are approximately 12% a year. The crowd, called to respond in kind and in faithfulness, was uplifted and inspired. The nobles and officials promised to do all that Nehemiah had requested. Having achieved this public agreement, Nehemiah was open and public in setting an abiding example of God’s generosity in his own life. Question: How does he do this? In verse 14, Nehemiah tells us that he served as governor of Judah for twelve years, from 445 B.C. to 433 B.C. He had real power for a long term. He had the right to certain privileges, gathered at the expense of the people; privileges that all Persian governors in all provinces took. One of these was a food allowance. Nehemiah tells us that he and his fellow administrators never claimed this during their term. In addition to collecting taxes for the central treasury, the king’s treasury, each provincial governor was entitled to collect for his own needs and his own personal treasury. Nehemiah tells us that his predecessors had historically exacted a heavy toll from the people in food, wine, and currency. Even lesser administrators, ‘servants’, had been permitted to do this. Nehemiah did not continue this practice because of his reverential “fear of the Lord. In verse 16 he tells us that his focus was on rebuilding the Jerusalem wall, and he never engaged in any speculation, primarily land speculation, that would have added to his personal wealth. As governor this temptation and opportunity was constantly available to him. As governor, he was required to set an extensive table each day, receiving and feeding as many as 150 officials, dignitaries from surrounding nations, and other visitors. He paid for all of this out of his own pocket for 12 years. At the end of this passage, Nehemiah asks God to remember him with favor for all that he has done for the people. This may sound self-aggrandizing but it’s not. Nehemiah is asking that God establish the value of his service, and that God accept his faithfulness. He is not asking for wealth, power, fame, immortality, or the destruction of his enemies. D. L. Moody wrote, “A holy life will produce the deepest impression. Lighthouses blow no horns; they only shine.” Nehemiah shines. Warren Wiersbe (Be Determined) writes that chapter 5 teaches us four important things: First, problems will arise among the people we lead or work with, no matter how noble the enterprise, how well-intended the efforts, and regardless of whether we share the same faith. Second, God calls on us to address these problems with his courage, his counsel, his restraint, his valuing of other lives and voices, and his honesty and encouragement. Third, we must act with God’s integrity as we strive to help solve the problems and challenges that arise. This requires sacrifice, vigilance, self-reflection, continuous prayer, and keeping God at the center of everything. Fourth, each problem, each challenge, each difficult or disappointing moment is an opportunity for God to work; to demonstrate his character, demonstrate his presence in the world, and demonstrate his faithful lovingkindness, mercy, and divine wisdom."
