    paragraph3: 'When the Jerusalem wall was completed in 52 days the significance was far greater than raised stone and doors hung at the gates. All the competing peoples who had claimed the Promised Land for themselves, the Samaritans, the Arabs, the Ammonites, and the remnants of the Philistines all knew that something  far greater had happened. God had intervened on behalf of his people to reassert their rightful place in the Promised Land.',
