    content: 'As Nehemiah deals with the speculative, advantage taking behavior of his own people, while fighting to keep focus on the completion of the wall, his external enemies renew their creative efforts to derail the project. In chapter 6 Nehemiah tells us that Sanballat, Tobiah, and Geshem, “and the rest of our enemies” heard that the wall was nearing completion, lacking only the doors for each gate. They send repeated letters to Nehemiah asking him to meet with them at a location 19 miles west and north of Jerusalem. This is near to the Mediterranean coast, around the location of modern-day Joppa. It would have been a day’s travel away from Jerusalem and much closer to the border of Samaria; a perfect staging ground for assassination. Nehemiah knows this and won’t travel to meet Sanballat and his cronies. Rather than ignoring Sanballat’s letters, and there were four of them, Nehemiah answers each one. Question: What does Nehemiah say and why does he answer each letter? Nehemiah sends messengers to Sanballat saying, “I am doing a great work and I cannot come down. Why should the work stop while I leave it and come down to you?” He responds in exactly this way to each of the four letters. What we know is that Nehemiah’s words about “a great work” didn’t refer to his own private actions but to the work that God had instructed him to do. We are able to draw this conclusion because Nehemiah’s behavior up to this point was impeccable, and observed by everyone; the kind of selfless, servant leadership that God and Christ truly value. Hebrews 13: 7 says, “Remember your leaders, those who spoke to you the word of God. Consider the outcome of their way of life, and imitate their faith.” This is Nehemiah in both word and action. Nehemiah intentionally creates a record, which became known to everyone in the province, documenting that he steadfastly turned down Sanballat four times in favor of God. Our accountability to God is to reject evil not once but always, and to be vigilant in discerning its intentions. Proverbs 8:13 says, “The fear of the LORD is hatred of evil. Pride and arrogance and the way of evil and perverted speech I hate.” Sanballat is quite clever. He sends repeated messages so that he could claim he had done his best to work with Nehemiah, and Nehemiah refused him. His letters are also to Nehemiah alone, not to the entire Jewish leadership; attempting to isolate Nehemiah and fuel speculation about what Nehemiah might or might not do. He is also trying to wear Nehemiah down. Nehemiah does persist in refusing Sanballat, but in a consistent and open way that cannot be misinterpreted. Nehemiah is proclaiming that this situation is not a personal match between Sanballat and him, but about God and God’s greater purposes. Nehemiah reminds us here that we should model our lives with Christ in all of our actions, our language, and in our responses to forces that seek to corrupt or undermine us. Paul captures this in Corinthians, chapter 3:1-3 and 23-25: “If then you have been raised with Christ, seek the things that are above, where Christ is, seated at the right hand of God. (2) Set your minds on things that are above, not on things that are on earth. (3) For you have died, and your life is hidden with Christ in God. (4) When Christ who is your[a] life appears, then you also will appear with him in glory…. (23) Whatever you do, work heartily, as for the Lord and not for men, (24) knowing that from the Lord you will receive the inheritance as your reward. You are serving the Lord Christ. (25) For the wrongdoer will be paid back for the wrong he has done, and there is no partiality.” Sanballat is the very wrongdoer that Paul describes. He is also creative and persistent. Verse 5 tells us that his fifth letter to Nehemiah was no longer private, but unsealed so that it would be read by a host of people before it got to Nehemiah. It accused Nehemiah of treason, and again did this with great shrewdness. He accused Nehemiah of placing prophets in Jerusalem who proclaimed that Nehemiah was the new king of Judah. Sanballat was escalating the stakes and seeking to take Nehemiah’s life. His letter was a naked and incendiary accusation aimed at stirring fear and anger among the Jews. The last thing the Jews wanted was for the Persian king to think that they were fomenting treason. It would be the end of everything. If enough Jews decided that these accusations of treason made Nehemiah too much of a risk as a leader, they could have deposed him or killed him. If Sanballat could not get to Nehemiah he was hoping that the Jews would do it for him. This should help us remember that evil is active and persistent, and may become more so in the face of great faith. It also provides moments for God to demonstrate his faithfulness to his people. This had to infuriate Nehemiah and also made him think about the risks he was taking in Judah. But Nehemiah didn’t bend or compromise, despite the frustration and weariness he must have felt. But why? There is little evidence that Nehemiah received any support from the other Jewish leaders. It is because Nehemiah trusted God and spoke to God continuously in a personal and open way. Proverbs 3:8 says, “Trust in the LORD with all your heart and lean not on your own understanding.” Psalm 112 says, “Light dawns in the darkness for the upright; he is gracious, merciful, and righteous. (5) It is well with the man who deals generously and lends; who conducts his affairs with justice. (6) For the righteous will never be moved; he will be remembered forever. (7) He is not afraid of bad news; his heart is firm, trusting in the LORD. (8) His heart is steady; he will not be afraid,..” Nehemiah has placed his trust in God and won’t be moved. His trust enables him to respond to Sanballat, in verse 8, with with fearless honesty and clarity, “What you say is not happening and you are making it up.” Nehemiah knows that Sanballat’s goal is to disrupt the last work in finishing the walls and gates. At the end of verse 9 Nehemiah returns again to God and asks him to strengthen his hands. The second challenge Nehemiah faces in chapter 6 is even more dangerous because it comes from inside; from the exiled Jews themselves. Nehemiah goes to visit Shemaiah who was most likely a priest, since he had ready access to the temple. Question: What is the danger and what is Nehemiah’s reaction? Shemaiah was a Jew and someone that Nehemiah knew, at least fairly well, because he’s visiting him at home. Shemaiah might have been isolated at home because he was unclean, but this would make no sense in light of his suggestion that they enter the temple together. The home isolation was probably self-imposed as a dramatic gesture of prophetic contemplation, or demonstration that Shemaiah feared for his own life. It was also a clever way to get Nehemiah alone. Shemaiah presented his prophecy and solution in dramatic verse. “They are coming to kill you Nehemiah, they are coming to kill you in the night, so let’s go together and lock ourselves in the inner sanctuary of the temple where we will be safe.” With a straight face Shemaiah is suggesting that Nehemiah flee and hide in the temple sanctuary, where only priests are permitted to enter. For Nehemiah, who was not a priest, it would be a violation of Mosaic Law, punishable by death, to enter the temple sanctuary. The danger here is that Shemaiah is posing at both Nehemiah’s friend and an expert; someone who has Nehemiah’s best interests in mind and only wants to protect him. God had given Nehemiah the composure and discernment to see through this. To us Shemaiah’s antics seem stupid and unbelievable. But remember that Shemaiah was most likely a priest, and is presenting himself as a prophet, which would have given him considerable status and credibility. Nehemiah’s response to this is resolute, fearless, and clear, “Will a man like me take flight? Will I enter the sanctuary to stay alive? I will not go!” Nehemiah’s answer is twofold: he is a leader raised up by God and needs to set the right example even if it puts his life at risk, and he’s not a priest. He can’t break God’s law by entering the temple sanctuary. Nehemiah tells us that God gave him greater understanding once Shemaiah suggested that Nehemiah break the law. He then knew that Shemaiah was a false prophet in the service of Tobiah and Sanballat. Shemaiah’s deception means that there were individuals and families among the returned exiles who didn’t want Nehemiah to succeed. Nehemiah noted that the prophetess Nodiah and other prophets sought to make Nehemiah fearful enough to abandon his mission. The opposition Nehemiah faced was extensive, well organized, and professional. They invited Nehemiah to stray far from Jerusalem, and then invited him into the very heart of his faith, the inner sanctuary of the temple. What is important for us to remember is that Nehemiah keeps his focus on God. He takes each challenge and temptation to God. He doesn’t lash out at Sanballat, Tobiah, Shemaiah, and Nodiah, he names them to God and asks God to deal with them. Because Nehemiah trusts God he has a true compass, anchor, and protection as he navigates these challenges. Again Paul understood this so well. In 2 Corinthians, chapter 10 he wrote, “(3) For though we live in the world, we do not wage war as the world does. (4) The weapons we fight with are not the weapons of the world. On the contrary, they have divine power to demolish strongholds. (5) We demolish arguments and every pretension that sets itself up against the knowledge of God, and we take captive every thought to make it obedient to Christ.” When the Jerusalem wall was completed in 52 days the significance was far greater than raised stone and doors hung at the gates. All the competing peoples who had claimed the Promised Land for themselves, the Samaritans, the Arabs, the Ammonites, and the remnants of the Philistines all knew that something far greater had happened. God had intervened on behalf of his people to reassert their rightful place in the Promised Land. What is instructive for us is that even in this remarkable moment when God had so clearly demonstrated both his presence and merciful intervention, Jewish nobles were still maneuvering by exchanging letters with the malignant Tobiah, and then trying to convince Nehemiah of Tobiah’s good deeds. Again Nehemiah saws through this and recognized that Tobiah was using this exchange of letters as another way of instilling fear, doubt, and distraction. The Lord is Nehemiah’s rock and purpose: Psalm 32 says, “You are a hiding place for me; you preserve me from trouble; you surround me with shouts of deliverance. Selah (8) I will instruct you and teach you in the way you should go; I will counsel you with my eye upon you.” God gives us a stability and reassurance, and capacity to endure stress and conflict that transcends our own capabilities, and in turn grants us a wisdom and discernment that is far greater than the world could ever muster. Under the new covenant this is the gift of the Holy Spirit.'
