    content: 'At the beginning of Esther full focus is on king Xerxes and his retinue. Question: What do we learn about Xerxes, his kingdom, and his closest advisors? Xerxes possesses great and formidable power, ruling over a vast domain, extending from the border of modern India, into Egypt, and beyond to modern Libya, Sudan, and Ethiopia (Cush). He believed in the power of lavish and excessive display, creating a banquet for all of the military leaders and provincial rulers from across his empire. Warren Wiersbe notes that Xerxes probably brought his provincial civil and military leaders in on a rotating schedule, which is why the banquet lasted for nearly six months, with all of the wealth of empire arrayed to greet each of his guests. Verse four tells us that Xerxes put all of these riches on display to prove how great he was. What this shows us is that Xerxes is utterly selfish as to the needs of state or his people. All resources were focused on him and his desires regardless of cost or burden. Scripture does not tell us the precise purpose of this extended celebration but Herodotus, the Greek historian, may offer insight. Herodotus wrote that Xerxes conferred at length with his leaders concerning his plans to subdue both Greece and what is now most of modern Europe. So Xerxes was avidly focused on augmenting his power, and had grandiose notions as to what this would look like, in addition to demonstrating his greatness. Give him credit for being a planner. He staged this extended banquet, framed it with an extraordinary display of his wealth, and then culminated the entire event with a seven day banquet within the enclosed garden of his winter palace in Susa. The royal wine is abundantly served and Xerxes encourages his guests to drink way too much. This is designed to probe weaknesses, loosen tongues, and produce an artificial gratitude. And it seems to work. Chapter one offers no evidence that any of Xerxes’ leaders questioned his planning or the wisdom of this extended banquet. Xerxes himself overindulges. We know this because verse 10 tells us that on the seventh day, when the king was in high spirits from drinking, he commanded his seven eunuchs who formed the inner circle of his servants, to fetch Queen Vashti. So we have an insider’s view of Xerxes’ impulsive and imperious nature. He wants her there not because he misses her or values her. He wants to put her on display along with his wealth, because as verse 11 tells us, “she was lovely to look at.” Vashti doesn’t; show up, and the implication is that she didn’t want to be displayed as a lovely possession in front of large numbers of filthy drunk men, including the king. Because Xerxes is a king and utterly self-regarding Queen Vashti’s refusal is not simply a family of squabble but a matter of state and the king’s power. She has refused to obey the king and must be dealt with. Her disobedience becomes grounds for admonishing all the women who are married to his military and civil leaders. Vashti is banned from the king’s presence and stripped of her title as Queen. Decrees are issued to the four corners of the kingdom demanding that men be given full respect and obedience as masters and rulers over their households. We see in this excess Xerxes’ great vanity, pomposity, and foolishness. He is a man without sound wisdom.'
