    content: 'What makes Esther’s courage in approaching Xerxes on behalf of Mordecai and the Jews living in exile even more remarkable is the intentional isolation she has been forced to live in. She is kept apart from the king for long stretches of time, and purposefully shielded from news of the world around her. Esther did not know about Haman’s well publicized edict commanding extermination of the Jews until Mordecai shared it with her through the court eunuch Hathatch. Esther’s life is defined by leisure, banquets, the excessive drinking that the Persian’s loved, and a complete lack of privacy as she lived out her life in the presence of a host of servants; some spying on behalf of the king, and others spying on behalf of the king’s inner circle. The culture of Xerxes’ court was not designed to encourage the power and prestige of women but to minimize them. It would have been easy for Esther to be numbed by all this, and reduced to passive inaction, but she rises above the corrupting softness and indulgence, and risks all in the name of God and his chosen people. We see in her actions the reflection of David’s own journey and his words in Psalm 86, words Mordecai and Esther may have known: “(14) O God, arrogant men attack me; a gang of ruthless men, who do not respect you, seek my life. (15) But you, O Lord, are a compassionate and merciful God. You are patient and demonstrate great loyal love and faithfulness. (16) Turn toward me and have mercy on me! Give your servant your strength! Deliver your slave! (17) Show me evidence of your favor!” That night, after the first banquet, Xerxes’ cannot sleep, so he has the chronicles of his reign read to him. This is a poignant moment. You can almost hear Xerxes’ longing for significance and affirmation in commanding this reading: ‘Have I been a good king? Will I be remembered?’ In Xerxes we see earthly power without God. Even this only leads to corruption, decay, and death. Xerxes has a sense of this however brief. God is near him this night. Remember that the chronicle is always updated in the king’s presence so the story it tells is designed to be flattering to the king. Xerxes commands the attendants to read, going back at least five years before, because the record recounts the saving service done by Mordecai in disclosing the plan by Bigthana and Teresh, two of Xerxes’ trusted guards, to assassinate him. He is startled afresh by the account and wants to know immediately if Mordecai was ever rewarded. God has used this moment with exquisite timing. Xerxes is listening, open, sober, and there isn’t that vast noise of his court trying to influence him. He’s thinking about his own legacy and mortality as king, and the perfect opportunity arises for him to confirm his power, his largess and liberality, and to cement the loyal of one of his subjects by finally rewarding Mordecai. It’s the sort of impulsive behavior we have seen repeatedly in Xerxes, but this time God uses that character flaw to accomplish his specific purposes. Question: This is a pivotal moment that begins the decisive shift of power and influence away from Haman and to Esther, Mordecai and the Jews. What does this teach us? God can reach anyone at any time. He is not subject to our schedules or limited imaginations. In this moment of great influence, Xerxes is about alone as he ever is, trying to sleep and failing. There are no state dinners, no formal meetings, no phalanx of advisors, or intricate plots. There is a king in his sleeping attire reminded of the extraordinary good service rendered by one of God’s people five years before. Nor does God need for Xerxes’ to suddenly become godly, good, or wise; he uses Xerxes’ own nature to accomplish his greater purpose which is to bring attention to Mordecai and Mordecai’s faithfulness which will help lead to deliverance for the exiled Jews. The works we, as believers, do in God’s name never go to waste. Sometimes we think God is not listening or that we have failed because we don’t get the effect or result we long for, at the moment we want it. God’s view is much longer than ours. When Mordecai saved the king from assassination and received no recognition in return, and then had to watch Haman’s elevation, he must have felt that God was ignoring him and the complete lack of any justice for his faithfulness. Five years later God directs Xerxes’ sleepy mind to Mordecai’s service and the result is far more powerful and useful than anything Mordecai could have imagined or wanted. Our confidence should first be in God and not our own design. The Book of Esther is thick with plotting, maneuvering, and state craft but neither Mordecai nor Esther could have predicted or engineered the outcome that God brought about. We should not take God’s intervention for granted or lightly assume that we may predict it. We need to seek him with faithfulness, persistence, and humility and should not be put off by the doubts or dry times that will certainly occur. Christ gave us the model for this in the parable of the persistent widow in Luke 18:1-8: “Then Jesus told them a parable to show them they should always pray and not lose heart. (2) He said, “In a certain city there was a judge who neither feared God nor respected people. (3) There was also a widow in that city who kept coming to him and saying, ‘Give me justice against my adversary.’ (4) For a while he refused, but later on he said to himself, ‘Though I neither fear God nor have regard for people, (5) yet because this widow keeps on bothering me, I will give her justice, or in the end she will wear me out by her unending pleas.’” (6) And the Lord said, “Listen to what the unrighteous judge says! (7) Won’t God give justice to his chosen ones, who cry out to him day and night? Will he delay long to help them? (8) I tell you, he will give them justice speedily. Nevertheless, when the Son of Man comes, will he find faith on earth?”The widow is not presented in an endearing way and the judge despises her and ignores her as long as he can. Yet, said God, I listen, respond, and dispense mercy to you who believe in me in moments just like these, and with a motive driven by my love for you; don’t abandon your faith. Isaiah 55: 3, 4 & 6-9 capture God’s greater design in God’s own voice with startling power: “Pay attention and come to me! Listen, so you can live! Then I will make an unconditional covenantal promise to you, just like the reliable covenantal promises I made to David. (4) Look, I made him a witness to nations, a ruler and commander of nations….(6) Seek the LORD while he makes himself available; call to him while he is nearby! (7) The wicked need to abandon their lifestyle and sinful people their plans. They should return to the LORD, and he will show mercy to them, and to their God, for he will freely forgive them. (8) “Indeed, my plans are not like your plans, and my deeds are not like your deeds, (9) for just as the sky is higher than the earth, so my deeds are superior to your deeds and my plans superior to your plans.” Ironically Haman cannot wait to have Mordecai impaled, so is waiting to see Xerxes’ at the crack of dawn. He has already authorized construction of the gallows. Question: What do we need to understand about the true character of the wicked? Psalm 36: 1-4 gives us extraordinary insight. It’s written by David and it reflects a revelation that God gave David concerning the true character of the wicked: “(1) An evil man is rebellious to the core. He does not fear God, (2) for he is too proud to recognize and give up his sin. (3) The words he speaks are sinful and deceitful; he does not care about doing what is wise and right. (4) He plans ways to sin while he lies in bed; he is committed to a sinful lifestyle; he does not reject what is evil.” This is a perfect rendering of Haman’s character and heart. He is obsessed with his power and with doing harm to those who don’t fear or respect it. He hates the Israelites because, as God’s chosen people, they are his hereditary enemies, and he has committed his full influence and resources to destroying them. He literally can’t wait to do the next bad thing. Evil drive him. What is the answer to the formidable and active impulse of the wicked to do harm and to slander the name of God? David also supplies the answer in Psalm 36:5-10: “O LORD, your loyal love reaches to the sky; your faithfulness to the clouds. (6) Your justice is like the highest mountains, your fairness like the deepest sea; you preserve mankind and the animal kingdom. (7) How precious is your loyal love, O God! The human race finds shelter under your wings. (8) They are filled with food from your house, and you allow them to drink from the river of your delicacies. (9) For you are the one who gives and sustains life. (10) Extend your loyal love to your faithful followers, and vindicate the morally upright!” God’s love and justice are our refuge.'
