    content: 'While Haman is reeling from a punctured ego he is summoned to attend the second banquet set up by Queen Esther. Esther must now share her request with Xerxes in front of Haman. Remember that Esther’s circumstances have not changed. She has not been given greater freedom, or unlimited access to the king, or greater status; she has not been allowed to emerge from the managed isolation of her quarters. She does not know that Xerxes has had a sleepless night, and may not even know that Xerxes has forced Haman to honor Mordecai. As far as she knows Haman is as powerful as ever. It is remarkable that she has included Haman at the banquet. It would have been easier for Esther to have asked to speak to the king alone, outside of Haman’s presence, just as Haman sought to speak to Xerxes alone when he wanted to kill Mordecai. What Esther knows is that she has one moment in which to attempt to influence the king. Her courage, steadiness, humility, and wisdom are extraordinary; and a model for us in learning servant leadership. Question: Just as we see the absence of God in Haman’s character, we see the presence of God in Esther’s character. What evidence do we see in chapter 7? Esther’s beginning language, as she makes her petition, is gentle and modest. She does not assert her identity or privilege as queen. Xerxes is a giant jerk, an impulsive cad, and a megalomaniac but Esther addresses him with profound respect and deference, just like Daniel did, and Joseph did. Her words are all about how the king feels, “If I have found favor with you, O king, and if it pleases your majesty, grant me my life -- this is my petition.” Esther is also fearless in identifying herself personally with the request. Her opening words proclaim, “Xerxes if you don’t grant this then I will die too.” Esther’s language is plain and truthful. In verse 4 her description of the deed to come “For I and my people have been sold for destruction and slaughter and annihilation…” mirrors the language of the decree drawn by Haman himself in Esther 3:13. Haman, who is sitting there, cannot object, because Esther is essentially quoting him. She also frames the dire nature of the situation for Xerxes, saying, ‘if this weren’t such a horrible thing, if it was only about further enslavement, I would not bother you with this.’ Notice too that Esther does not jump the gun. She does not start with accusation; she starts with an even-handed description of the issue; just like the prophet Nathan, when he confronted David about Bathsheba. Esther also demonstrates that she is a great leader. In this moment she is the Queen of her exiled people and has taken up their cause, at the highest level, when no one else can. God has indeed appointed her for this moment. We see Xerxes at full impulse. He has no recollection of the decree he’s signed against the Jews and demands to know “where is the man who has dared to do such a thing?” We see several things here. Xerxes demonstrates the historic tendency of all autocrats to not take responsibility for their actions. He gave Haman his signet ring and his authorization but that is now conveniently forgotten. Also, kings, whether good or bad, tend to be notoriously busy and move on from past decisions to the next thing. Esther recognizes all this in how she frames her request. Esther is observing the admonition found in Ephesians 4:29, “Let no corrupting talk come out of your mouths, but only such as is good for building up, as fits the occasion, that it may give grace to those who hear.” Remarkably, even as she makes her petition, Esther is also giving grace to Xerxes. We also see something akin to the Holy Spirit at work in Esther, reflecting what Paul wrote in I Corinthians 12:7-9, “(7) The Holy Spirit is given to each of us in a special way. That is for the good of all. (8) To some people the Spirit gives a message of wisdom. To others the same Spirit gives a message of knowledge. (9) To others the same Spirit gives faith.” We clearly see God at work in Esther, and Esther’s character becomes like God’s. She speaks with an inspiration, temperance, and self-abnegation that is beyond the reach of human action, and clearly within the realm of God. The rest of chapter 7 is dark. Esther accuses Haman – “the adversary and the enemy is this vile Haman” - and Xerxes has him executed on the very seven story gallows that Haman built for Mordecai. Question: Some commentators say that Esther should have exercised more charity and mercy here, rather than accomplishing Haman’s execution. Do you agree? Remember that Esther, like Mordecai, is from the tribe of Benjamin, which was also the tribe of King Saul, and that Saul lost his right of kingship because he disobeyed God’s command, shared through the prophet Samuel, to attack and exterminate the Amalekites and their king, Agag, who were devout enemies of Israel. Now a second monarch from the tribe of Benjamin faces a blood descendant of the Amalekites, whose evil malice in the service of Xerxes and threat to annihilate the exiled Jewish population is directly traceable to Saul’s disobedience. Esther will not make the same mistake. Haman is not repentant or regretful; verse 6 tells us that he was terrified and undone that Xerxes had turned against him. He begs Esther for his life, not for forgiveness. God knows Haman’s heart and is administering the justice that Haman deserves. As Paul wrote in Galatians 6: 7-8, “Do not be deceived: God is not mocked, for whatever one sows, that will he also reap. 8 For the one who sows to his own flesh will from the flesh reap corruption, but the one who sows to the Spirit will from the Spirit reap eternal life.” Remember that Haman still wears the king’s signet ring and carries the power of Xerxes wherever he goes. Given Xerxes’ distracted state, Haman could have fomented rebellion against Xerxes himself and then fulfilled his plan to exterminate the Jews. Esther cannot allow that possibility. In addition, the only way to counteract the original edict of extermination was to create a new edict that granted the Jews a greater right of self-defense that the original right to exterminate them. Haman’s power, which is in the service of evil, must end. Throughout scripture God makes clear that the wicked will receive his justice. Psalm 147: 5-6 says, “Great is our Lord, and abundant in power; his understanding is beyond measure. (6) The LORD lifts up the humble; he casts the wicked to the ground” Haman is literally clutching at Esther to beg for his life. When Xerxes returns from the palace garden he assumes that Haman is trying to rape Esther. By law no one was ever to be closer than seven steps from the king’s concubines. This would include the queen. This becomes the grounds for Xerxes’ first charge against Haman. The second charge arises when the eunuch Harbona notes that the 75 foot tall gallows was constructed by Haman to kill Mordecai, who has become a benefactor and favorite of the king. Plotting to kill Mordecai, now under the king’s protection, becomes treason and seals Haman’s execution. Haman’s own design becomes his undoing. '
