    content: 'We get a strong sense of the division and volatility within the Persian Empire by the reaction to the two edicts. There is still a strong element across many of the 127 provinces that is committed to attacking the Jews despite the issuance of the second decree and the fall of Haman. These are probably allies who remain true to the power of Haman’s household, opportunists who want to seize additional wealth (plunder), and those who view this war of competing edicts as a referendum on the power of Xerxes and Mordecai. At this point in his reign Xerxes is not beloved by his people. His failed expeditions against the Greeks have resulted in catastrophic casualties, huge investments, near bankruptcy, and much higher taxes. Question: Are the Jews excessive or vindictive in their response? Although there is some fuzziness in the language it is most likely that the Jews are attacking only those who attack them first. The enemies arrayed against the Jews are seeking not simply to defeat or embarrass them, but to wipe them from the face of the earth. Part of the Jewish response is to simply ensure that the people seeking their annihilation aren’t around to do it again. Part of this battle is completing the holy war against the Amalekites; and in aid of this they kill the 10 sons of Haman who have taken up arms against them. Queen Esther requests that their dead bodies be put on public display (9:13) just like the bodies of King Saul and his sons were put on display by the Philistines at Beth-Shan. Esther’s request to extend the edict for one more day may seem blood thirsty or punitive but it’s really not. Many commentators view this as excessive vengeance or the triumph of blood lust but there are practical reasons for Esther to make her request. There is a pitched battle going on across the 127 provinces of the empire, and it will take more than one day to resolve. What is unspoken here is that the Jews must have suffered losses as well, even though permitted to take up arms in self-defense, and even though they have the support of Xerxes’ nobles, satraps, governor, and administrators. This is a messy moment and we can imagine that the enemies of the Jews are fighting as hard as they can to achieve the results promised in the first edict, and not the second. As Warren Wiersbe writes (Be Committed: Doing God’s Will Whatever the Cost, David C. Cook, 1993, p. 180, “The fear of God protects those who fear God and believe His promises. Because the Jews believed Mordecai’s decree, they had new courage and were not afraid of the enemy, and their courage put fear into the hearts of the enemy.” Esther’s own actions state her confidence in God’s outworking in her world. Perhaps she is remembering verses from Psalm 22: “(29) All the ends of the earth shall remember and turn to the LORD, and all the families of the nations shall worship before you. (28) For kingship belongs to the LORD, and he rules over the nations.” Book of Esther: A Hard Victory and a Profound Remembrance. Haman and Xerxes’ first edict and the second correcting edict allowing the Jews to gather and defend themselves have resulted in a small scale war. Verse 16 of chapter 9 tells us that at the end of the fighting across the 127 provinces the Jews have killed 75,000 Persians in defending themselves. This does not count wounded or captured or calculate the number of Jews killed and wounded. This casualty level states a much larger number of Persians who joined in the attack against the Jews. It means that the Jews, in defending themselves, and even in having the help of the king’s administration, may still have been outnumbered in many instances. They fought across a vast territory and amidst a large foreign population mostly ambivalent and probably often unwilling to assist them. To frame this, Guinness world records says that 49 million of the world’s estimated 112 million people lived in the Persian Empire in 480 BC. Keep in mind that God does not deliver the Jews from conflict, but does enable them to enter the conflict and overcome. Verses 15 and 16 also tells us that the Jews in Susa, and the Jews in the 127 provinces, did not take any plunder from those they defeated. To not take the property of the defeated was to behave differently than every other people, nation, or culture at this time in history. Plunder was the accepted right of the victor, and a sensible practice at a time when resources were scarce and survival from year to year was a high risk proposition. Under the second edict the Jews were granted the right to take the property of those who attacked them. Question: What does this refusal to take plunder tell us? This is affirmative proof that the Jews had not forgotten God; and that the Book of Esther does not simply offer a recounting of secular events. It is very similar to Abraham’s decision to not take plunder after defeating Kedorlaomer and three other kings in rescuing Lot. In Genesis 14:22 Abram said, “I have raised my hand to the LORD. God Most High, Creator of heaven and earth, and have taken an oath that I will accept nothing belonging to you, not even a thread or a thong of a sandal, so that you will never be able to say, ‘I made Abram rich.’” After this fearless testimony, God comes to Abram in a vision, in Genesis 15:1, and says, “Don’t be afraid, Abram. I am your shield, and your great reward.” So, by their actions rather than spoken words, the Jews were announcing to everyone around them that they looked to God and acknowledged him as the source of their deliverance and provision. They were expressing their confidence, that even in exile, God was with them and had not abandoned them. This decisive act of faith must have spooked the Persians. It affirmed God’s view that this was a holy war, and not a regional conflict. It had further symbolism in correcting Saul’s error, long before the reign of Xerxes and the time of exile, in keeping plunder from the Amalekites when God told him not to. Following this conflict, and their deliverance the Jews paused and celebrated. In rural villages the conflict ended sooner than in the cities so the celebrations crossed over several days. What is interesting is that the Jews celebrated on their own. This was not commanded by God but was very much in keeping with his character in remembering and celebrating great moments of God’s faithfulness such as Passover. Mordecai observed this celebration and then formalized it in verses 20-22, with a letter sent to all the provinces, directing that all Jews celebrate this occasion each year, on the 14th and 15th day of Adar. This celebration became Purim, meaning ‘lot’ referencing Haman’s roll of the dice in Esther 3:7 to determine the day, month, and year in which the Jews were to be massacred. Verse 29 tells us that Esther and Mordecai, together, write, with full authority, a second letter, confirming that Purim would always be celebrated as Mordecai directed in the first letter. This letter is described in verse 30 as “words of peace and truth. Question: What is the significance of Purim and what do we, as followers of Christ, learn from the Book of Esther? God uses the common, the ordinary, the imperfect, the dispossessed, the exiled, the weak, the poor, the endangered, the beleaguered, and the oppressed to accomplish his great and majestic purposes. This cannot be emphasized too strongly or repeated too often. Esther had great beauty and Mordecai was a low level court official but they did not have wealth, preference, or distinction in the Persian Empire. They were exiles, with no rights, subject to the rule of a vast empire. God uses this approach so often because the weak have no delusions about where deliverance and salvation come from. Think of Mordecai’s raw lament and unembarrassed display of weeping, sack cloth, and ashes. If we saw him coming down the street we would cross to the other side. David, even when he was king, knew about God’s love and great compassion for our weakness and infirmity. In Psalm 61 he wrote, “Hear my cry, O God; listen to my prayer. From the ends of the earth I call to you, I call as my heart grows faint; lead me to the rock that is higher than I. For you have been my refuge, a strong tower against the foe.” We must join Esther and Mordecai in looking up to God. Reach out to him when you are at lowest ebb. He will take hold and not let go. God enters history, he enters our daily lives, he enters into places that we view as dark and forbidden, nothing withstands him. The term ‘Purim’ really tells us that God’s power, and providential care for his people are greater than Haman’s power and greater than his cold casting of the dice. In Isaiah 45 God says “I am the LORD, and there is no other. I form the light and create darkness; I bring prosperity and create disaster. I, the LORD, do all these things” During the Holocaust Jews took great care to preserve and share the text of Esther; and to remember God’s actions in saving his people from extinction. Women stand equally with men before God and Christ; and God uses them powerfully in leading and accomplishing his work. God elevates Esther to a position of power and uses her to accomplish his greater purpose by making her a superlative, if flawed leader. By the end of chapter 9 Esther is called Queen Esther, daughter of Abihail, signifying that God has brought her to full maturity as both queen of Persia and queen of her people, the chosen people of God. She served God with courage, fierce persistence, humility, and wisdom greater than her soul; because she chose to trust God, and follow him rather than giving counsel to her fears. She kept God’s instruction in Isaiah 41:10, “So do not fear, for I am with you; do not be dismayed, for I am your God. I will strengthen you and help you; I will uphold you with my righteous right hand.” Faith begins with each individual but is grounded in the community of believers in Christ which today is the Church. Esther and Mordecai worked together to address the challenges they faced. They didn’t abandon one another, resent one another, or betray one another. Esther put Mordecai forward at risk to her own reputation, and Mordecai was steadfast in putting his people first, and not seeking his own preferment. Mordecai also kept Esther informed when she has no other sources of outside information and reminded her of God’s greater purposes for her at the critical moment. Finally Esther reminds us that even when everything around us seems to degrade, devalue, mock, and override our very faith, and the very presence of God, He is still there, and we are his chosen people; inhabited by his divine majesty, mercy, wisdom, power, love, and grace. We may travel dark paths in the world but we are set apart as his children of light. As Peter wrote in I Peter 2:9 “But you are a chosen peple a royal priesthood, a holy nation, a people belonging to God, that you may declare the praises of him who called you out of darkness into his wonderful light.” So the end is not the end. The deliverance for the Jews occurs in 473 B.C. and Xerxes reign continues until August 465 B.C., eight years later, when Xerxes and his son Darius are assassinated by Artabanus, the leader of the king’s body guard, with the help of a eunuch Aspamitres. Artaxerxes, the third son of Xerxes, seized power by killing Artabanus. In the Book of Ezra, Artaxerxes commissions Ezra, to take charge of Israel. Ezra returns to Israel in the seventh year of Artaxerxes reign. In the 20th year of Artaxerxes’ reign, Nehemiah is commissioned to return to Jerusalem to rebuild the walls of the city. We don’t know Esther’s fate or journey. Some say that she was the mother of Darius II who became king after Artaxerxes. Some say that Esther is the queen mentioned in Nehemiah 2:6, sitting besides Artaxerxes in the 20th year of his reign. Some say she died when she was still young and others that she lived to an old age. We don’t know.'
