    image: 'https://davyjonesdesign.github.io/data-for-axios/assets/wmstudies/matthew.jpg',
    paragraph1: 'There is no decisive agreement among scholars that the disciple Matthew wrote the gospel of Matthew. Scholars tend to reach a hesitant consensus in suggesting that the apostle Matthew is the writer for a couple of reasons. First, and most importantly, identification of Matthew as the writer is made by early church leaders. Second, as Craig S. Keener notes, if Matthew didn’t write the gospel, attaching his name to it is an odd choice, given the starkly bad reputation of tax collectors, even reformed ones. Names like Peter or Andrew would, at the start of the Christian faith, have conferred greater credibility and authority.',
    list1: [
      {
        item: 'Concerning the opinion of early church leaders: Eusebius of Caesarea (c. 330), in his own writing, quotes an earlier statement made by Papias (Bishop of Hierapolis in Asia Minor perhaps as early as 110) concerning the origins of the gospels of Mark and Matthew. This is what Eusebius wrote: ',
        quote: 'The Elder used to say: Mark, in his capacity as Peter’s interpreter, wrote down accurately as many things as he [Peter?] recalled from memory — though not in an ordered form — of the things either said or done by the Lord. For he [Mark] neither heard the Lord not accompanied him, but later, as I said, [he heard and accompanied] Peter, who used to give his teachings in the form of chreia, but had no intention of providing an ordered arrangement of the logia of the Lord. Consequently Mark did nothing wrong when he wrote down some individual items just as he [Peter] related them from memory. For he made it his one concern not to omit anything he had heard or to falsify anything... Therefore Matthew put the logia in an ordered arrangement in the Hebrew language, but each person interpreted them as best he could.'
      },
      {
        item: 'In these comments “The Elder” means John the Elder, a follower of Christ. Chreia means  short units about a person’s words and deeds. In Greek rhetoric, the chreia form was a basic unit of biographical or historical writing. A comparable term might be “literary anecdote.”  Logia means “sayings.”   Scholars disagree as to the meaning of Eusebius’s comments about the orderliness of John Mark’s gospel and the identity of John the Elder,  but point to the direct identification of Matthew as the writer of the gospel of Matthew. (Richard Bauckham, Jesus and the Eyewitnesses (Eerdmans, Grand Rapids  2006) p. 203)'
      },
      {
        item: 'R.T. France (The Gospel of Matthew, (Eerdmans, Grand Rapids 2007), pp 18-19) writes that in his view Matthew was probably written before 70 A.D. while the Temple was still standing, and while it was far more likely that the apostle Matthew was still alive and at work. F.F. Bruce dates the first three gospels this way:  “Mark around AD 64 or 65, Luke shortly before 70, and Matthew shortly after 70.”  All three were written “when many were alive who could remember the things that Jesus said and did.” (F.F. Bruce, The New Testament Documents (Eerdmans, Grand Rapids 1981) p. 7)'
      }
    ],
    content: 'There is no decisive agreement among scholars that the disciple Matthew wrote the gospel of Matthew. Scholars tend to reach a hesitant consensus in suggesting that the apostle Matthew is the writer for a couple of reasons. First, and most importantly, identification of Matthew as the writer is made by early church leaders. Second, as Craig S. Keener notes, if Matthew didn’t write the gospel, attaching his name to it is an odd choice, given the starkly bad reputation of tax collectors, even reformed ones. Names like Peter or Andrew would, at the start of the Christian faith, have conferred greater credibility and authority. Concerning the opinion of early church leaders: Eusebius of Caesarea (c. 330), in his own writing, quotes an earlier statement made by Papias (Bishop of Hierapolis in Asia Minor perhaps as early as 110) concerning the origins of the gospels of Mark and Matthew. This is what Eusebius wrote: “The Elder used to say: Mark, in his capacity as Peter’s interpreter, wrote down accurately as many things as he [Peter?] recalled from memory — though not in an ordered form — of the things either said or done by the Lord. For he [Mark] neither heard the Lord not accompanied him, but later, as I said, [he heard and accompanied] Peter, who used to give his teachings in the form of chreia, but had no intention of providing an ordered arrangement of the logia of the Lord. Consequently Mark did nothing wrong when he wrote down some individual items just as he [Peter] related them from memory. For he made it his one concern not to omit anything he had heard or to falsify anything... Therefore Matthew put the logia in an ordered arrangement in the Hebrew language, but each person interpreted them as best he could.” In these comments “The Elder” means John the Elder, a follower of Christ. Chreia means short units about a person’s words and deeds. In Greek rhetoric, the chreia form was a basic unit of biographical or historical writing. A comparable term might be “literary anecdote.” Logia means “sayings.” Scholars disagree as to the meaning of Eusebius’s comments about the orderliness of John Mark’s gospel and the identity of John the Elder, but point to the direct identification of Matthew as the writer of the gospel of Matthew. (Richard Bauckham, Jesus and the Eyewitnesses (Eerdmans, Grand Rapids 2006) p. 203) R.T. France (The Gospel of Matthew, (Eerdmans, Grand Rapids 2007), pp 18-19) writes that in his view Matthew was probably written before 70 A.D. while the Temple was still standing, and while it was far more likely that the apostle Matthew was still alive and at work. F.F. Bruce dates the first three gospels this way: “Mark around AD 64 or 65, Luke shortly before 70, and Matthew shortly after 70.” All three were written “when many were alive who could remember the things that Jesus said and did.” (F.F. Bruce, The New Testament Documents (Eerdmans, Grand Rapids 1981) p. 7)'
