    content: 'Samuel Johnson, the great English essayist wrote, “When a man knows he is to be hanged in a fortnight, it concentrates his mind wonderfully.” As the crew members of the commercial cargo ship lift Jonah over the side into the surging water, as far as Jonah knows he has only moments to live and is about to disappear into a terrifying, cold-black, bottomless abyss. The Mediterranean Sea is deep, with an average depth of 4900 feet and 16,000 feet at its deepest point. As he goes over the side what does Jonah see and what is he thinking? Scripture tells us that as Jonah was interrogated by the ship’s crew and then confesses to them the storm continued to rise in ferocity. What did this look like? Psalm 107:23-30 gives us a good picture as does Paul’s own shipwreck in Acts 27. Huge waves, driving rain, lightning, the darkness of low clouds, the inability to see land or to discriminate between sky and water. The ship rising on wave after wave and then descending into deep troughs, water pouring relentlessly over every square inch of the deck, oars snapping and being pulled away by the storm surge, and no ability to control the direction of the ship’s bow or rudder. It was an endless, overpowering, claustrophobic, cyclone of wet terror, that left Jonah and the crew in a state of exhausted helplessness near-to-death. Jonah knows that God has sent all of this and may have felt like the Genesis judgment of the great flood had returned just for him, as part of God’s own judgment on his disobedience and flight. So how does he feel? Fear, unending stress, helplessness, grief, and terrible regret. Think about Jonah’s regard for himself. He identified himself first as a Hebrew. This means that the last hands that handle him before his death are the unclean hands of pagans, the very people Jonah despises and doesn’t want to help, not his own Jewish countrymen. Kevin J. Youngblood (Jonah: Exegetical Commentary on the Old Testament, p. 84) writes that Jonah submits to being thrown overboard by the pagan crew because he now values himself as no better than the cargo the crew has already jettisoned. At this point, even though Jonah knows that the pagans have been actively praying to their gods, and that the one true living God whom Jonah worships is responsible for the storm, Jonah will still not talk to God. He does talk to the pagans. In addition, Youngblood says that Jonah’s fate reflects how God had used and would continue to use other nations to chasten and punish Israel. The Jewish Virtual Library says, “The one thing expressed most clearly by Israelite burial practices is the common human desire to maintain some contact with the community even after death, through burial in one\'s native land at least, and if possible with one\'s ancestors. "Bury me with my fathers," Jacob\'s request (Genesis 49:29), was the wish of every ancient Israelite.” As far as Jonah knows that burial will not happen for him. His fellow Israelites will not have the chance to mourn his death, his body will not be prepared for burial in a tomb in the land of his own fathers. He will not lay in the earth of the Promised Land. For Jonah this would have been an unspeakable source of grief and loss, and in this moment he would have realized that his predicament was his own making. He had chosen to reject God, to distrust his own countrymen by not sharing his predicament with them, and to reject the Promised Land by leaving in secret. He had chosen to run, to escape by ship, to travel to a remote location and risk the passage across the Mediterranean.  Even though God created great fleets for Solomon, the people of Israel were not like the Phoenicians, the restless, far-reaching people of the sea. The Israelites were the people of the hills, the plains, and the desert. They were the people of God taught to first look up at the heavens, like Abraham, to behold the sheer vast creative majesty of the one true living God who had made them and chosen them. For the Israelites deep water was a mystical and dangerous vault. Ray Vander Laan writes that in scripture, the flooding waters of the sea became a tool of God\'s judgment. In Genesis chapter 7 all of the fountains of the great deep burst forth and the windows of heaven were opened, bringing a torrent of rain and an all-engulfing floodtide that erased all living things outside of Noah’s ark. The Psalms describe the sea as dangerous. Psalm 69:1-2 says, “Save me, O God! For the waters have come up to my neck. I sink in deep mire where there is no foothold. I have come into deep waters and the flood sweeps over me….” (Ray Vander Laan, That the World May Know) Yet in this moment of preparing for his own death Jonah receives an unexpected and remarkable postponement. Given the abundant clothing that Jonah was probably wearing as a prophet close to the king – long tunic over an undergarment and a long ankle-length mantle over the tunic woven from high quality linen or wool - he was probably well-underneath the water and beginning the act of drowning. Details from Jonah’s prayer in Jonah 2:5&6 tell us that he was surrounded by deep water, head wrapped in weeds, descending towards the bottom with no hope of regaining the surface of the sea. Jonah 1:17 tells us that God appointed a large fish which swallowed Jonah as he descended into the deep water and it is here, in the fish, that he remained alive for the next three days and three nights. The Prayer Many commentators in chapter two call Jonah’s prayer a prayer of thanksgiving for his deliverance. But there is much more going on than this. This cannot have been a moment of happiness for Jonah or even relieve. If the hold of the cargo ship where Jonah slept was like a self-imposed coffin, the inside of the fish was a smaller and unexpected one, and absolutely not pleasant or offering safety in a way that Jonah would trust or understand. The fish was an active creature of the deep and sounding the depths of the Mediterranean Sea. Jonah was in an utterly alien, claustrophobic space, which offered no escape routes. He had lost even the feeling of the ship’s wooden deck beneath his feet. The physical world as he understood it was completely gone. No land, no sky, no ability to tell up from down, and he is pinioned and unable to even move in the complete, stinking, ink-black darkness of the fish’s gut. Jonah has lost contact with all human companionship, whether good or unpleasant, even the pagan kind. All of the markers of his identity, all of his future expectations, ambitions, hopes, plans, common irritations and anxieties, and all of his connections with life are gone. It is like a living death. All that Jonah has left is God.  When Jonah ran he was seeking escape and complete anonymity. His every action screamed that he wanted to disappear in a final way so that even God would not be able to find him. Inch by inch God relentlessly gave Jonah exactly what he was asking for: on a ship, in a storm, in a fish, in deep water, in the vast expanse of the Mediterranean Sea. God intervenes in history and uses his creation so that Jonah has one choice left and only one: whether or not to talk to God, and he chooses to talk. He chooses prayer.  The structure of the prayer conforms to a psalm’s thanksgiving prayer but the circumstances give it a sober recognition. As Youngblood observes Jonah’s prayer recognizes God’s exercise of a severe mercy which may still result in Jonah’s death. Jonah has no guarantee that he will survive the fish or ultimately be saved. His discomfort and sense of dislocation are still off the charts. His recognition beginning in Jonah 2:2 is that God has used the fish to save him from drowning, and for this he is grateful. He recognizes in verse 3 that it is God himself who has delivered Jonah into the depths of the sea and brought on the storm that launched him overboard. In verse 4 Jonah recognizes that it is his own behavior that has been the cause of God’s extreme acts, but his recognition, in verse 4, is that God has not rejected him, and he will see God’s holy temple once again despite his current circumstances. In verses 6 and 7 Jonah describes what was actually happening to him as he sank and began to drown. God used the fish to rescue him the extremity and stark bleakness of that moment. Jonah realizes that it was only at the point of seemingly total loss that he began finally to pray to God again, and that his relationship with God is more important than life itself.  In verse 8 Jonah seems to harken back to the shotgun prayers of the pagan ship’s crew and their captain to every false god in hopes of being saved. Youngblood writes that this also reflects Jonah’s continuing struggle with the identify of other people unlike him and in need of relationship with God, and with the mission to Nineveh that God has asked him to undertake. God uses this example to remind Jonah that the hope of steadfast love can only be fulfilled in the one true living God, whom Jonah has been strenuously avoiding until he is lodged in the fish. In verse 9 Jonah confirms that his worshipful relationship and obedience to God is restored. Jonah’s last affirmation is what God has been looking for, “Salvation belongs to the LORD!” It is following this recognition by Jonah that God speaks to the fish and the fish spews a very moist Jonah to safety. God wants Jonah’s recognition to be our own: that God values us, invests in us, calls on us, and uses stark and harsh events around us to draw us closer to him, regardless of our imperfections and prejudices. It is only when Jonah has lost everything that has false priority and importance in his life that his relationship with God is restored. Notice that Jonah does not apologize to God for his flight and disobedience. Jonah’s vow to sacrifice to God with the voice of thanksgiving in verse 9 is the expression of his hope that God will forgive him and receive the offerings he will bring in atonement and affirmation. God has strenuously arrayed his powers and creation to remind Jonah of his active presence, reminding both Jonah and us of these words from Psalm 139:7-12 Where shall I go from your Spirit? Or where shall I flee from your presence? 8 If I ascend to heaven, you are there! If I make my bed in Sheol, you are there! 9 If I take the wings of the morning and dwell in the uttermost parts of the sea, 10 even there your hand shall lead me, and your right hand shall hold me. 11 If I say, “Surely the darkness shall cover me, and the light about me be night,” 12 even the darkness is not dark to you; the night is bright as the day, for darkness is as light with you. God What does God think of Jonah? It is easy to assume that he finds Jonah as annoying as everyone else does. Fortunately we have God’s own eyewitness testimony. In Matthew 12:38-41 Christ says this about Jonah: 38 Then some of the scribes and Pharisees answered him, saying, “Teacher, we wish to see a sign from you.” 39 But he answered them, “An evil and adulterous generation seeks for a sign, but no sign will be given to it except the sign of the prophet Jonah. 40 For just as Jonah was three days and three nights in the belly of the great fish, so will the Son of Man be three days and three nights in the heart of the earth. 41 The men of Nineveh will rise up at the judgment with this generation and condemn it, for they repented at the preaching of Jonah, and behold, something greater than Jonah is here. In verse 39 Christ refers to Jonah as one of God’s prophets and in verse 41 he observes that the men of Nineveh repented specifically in response to Jonah’s preaching. Hebrews 1:1-2, says, “Long ago, at many times and in many ways, God spoke to our fathers by the prophets, but in these last days he has spoken to us by his Son, whom he appointed to the heir of all things.” God loved and anointed his prophets to speak his truth to his people and the world before the arrival of Christ, and Jonah, by God’s own word and definition, stands in that prophetic lineage and beloved of God. And he was effective on God’s behalf. God regards Jonah as more precious, more important, more effective, and more beloved that Jonah regarded himself. God knew he could work with Jonah to help accomplish his greater purpose in the world. Today, reaching us through the redemptive power of Christ, God assigns a similar greater-than-life and abundant value to each of us and our capacity, in his name, to advance his kingdom.'
